<template>
  <section class="echartsTuoPu" :id="container"></section>
</template>

<script>
/* eslint-disable camelcase */
// eslint-disable-next-line no-unused-vars
import getTuoPuNode from '../../static/dailyOperationDate'

import hangxing_Blue from '@/assets/DailyOperation/tuopu/v1_blue1.png'
import hangxing_Red from '@/assets/DailyOperation/tuopu/v1_red1.gif'

import baopan_Blue from '@/assets/DailyOperation/tuopu/v1_blue2.png'
import baopan_Red from '@/assets/DailyOperation/tuopu/v1_red2.gif'

import KCXP_Blue from '@/assets/DailyOperation/tuopu/v1_blue3.png'
import KCXP_Red from '@/assets/DailyOperation/tuopu/v1_red3.gif'

import jiaohuanji_Blue from '@/assets/DailyOperation/tuopu/v1_blue4.png'
import jiaohuanji_Red from '@/assets/DailyOperation/tuopu/v1_red4.gif'

import zhongjianjian_Blue from '@/assets/DailyOperation/tuopu/v1_blue5.png'
import zhongjianjian_Red from '@/assets/DailyOperation/tuopu/v1_red5.gif'

import DB_Blue from '@/assets/DailyOperation/tuopu/v1_blue6.png'
import DB_Red from '@/assets/DailyOperation/tuopu/v1_red6.gif'

import serve_Blue from '@/assets/DailyOperation/tuopu/v1_blue7.png'
import serve_Red from '@/assets/DailyOperation/tuopu/v1_red7.gif'

import cipan_Blue from '@/assets/DailyOperation/tuopu/v1_blue8.png'
import cipan_Red from '@/assets/DailyOperation/tuopu/v1_red8.gif'

import Nginx_Blue from '@/assets/DailyOperation/tuopu/v1_blue9.png'
import Nginx_Red from '@/assets/DailyOperation/tuopu/v1_red9.gif'

import zongxian_Blue from '@/assets/DailyOperation/tuopu/v1_blue10.png'
import zongxian_Red from '@/assets/DailyOperation/tuopu/v1_red10.gif'

import zhongtai_Blue from '@/assets/DailyOperation/tuopu/v1_blue11.png'
import zhongtai_Red from '@/assets/DailyOperation/tuopu/v1_red11.gif'

import celuo_Blue from '@/assets/DailyOperation/tuopu/v1_blue12.png'
import celuo_Red from '@/assets/DailyOperation/tuopu/v1_red12.gif'

import order_Blue from '@/assets/DailyOperation/tuopu/v1_blue13.png'
import order_Red from '@/assets/DailyOperation/tuopu/v1_red13.gif'

import KCBP_Blue from '@/assets/DailyOperation/tuopu/v1_blue14.png'
import KCBP_Red from '@/assets/DailyOperation/tuopu/v1_red14.gif'

import PC_Blue from '@/assets/DailyOperation/tuopu/v1_blue15.png'
import PC_Red from '@/assets/DailyOperation/tuopu/v1_red15.gif'

import PHONE_Blue from '@/assets/DailyOperation/tuopu/v1_blue16.png'
import PHONE_Red from '@/assets/DailyOperation/tuopu/v1_red16.gif'

import frameBG from '@/assets/DailyOperation/tuopu/v1_frame.png'
/**
 * 图标
 * @hangxing 行情
 * @baopan 报盘
 * @KCXP KCXP
 * @jiaohuanji 交换机
 * @zhongjianjian 中间件
 * @DB 数据库
 * @serve 服务器
 * @cipan 磁盘
 *
 * @Nginx Nginx接入
 * @zongxian 消息总线
 * @zhongtai 中台服务器
 * @celuo 策略
 * @order 接入快速订单
 * @KCBP KCBP
 * @PC PC
 * @PHONE PHONE
 */
const tyepDefault = {
  '行情': 'hangxing',
  '报盘': 'baopan',
  'KCXP': 'KCXP',
  '交换机': 'jiaohuanji',
  '中间件': 'zhongjianjian',
  '数据库': 'DB',
  '服务器': 'serve',
  '磁盘': 'cipan',

  'Nginx接入': 'Nginx',
  '消息总线': 'zongxian',
  '中台服务器': 'zhongtai',
  '策略': 'celuo',
  '接入快速订单': 'order',
  'KCBP': 'KCBP',
  'PC': 'PC',
  'PHONE': 'PHONE'
}
const nodeFontSize = 14
// import echarts from 'echarts'
export default {
  name: 'echartsTuoPu',
  data () {
    return {
      baseNode: [],
      tyepDefault,
      /**
       * 图标
       */
      frameBG,

      Nginx_Blue,
      Nginx_Red,
      zongxian_Blue,
      zongxian_Red,
      zhongtai_Blue,
      zhongtai_Red,
      celuo_Blue,
      celuo_Red,
      order_Blue,
      order_Red,
      KCBP_Blue,
      KCBP_Red,
      PC_Blue,
      PC_Red,
      PHONE_Blue,
      PHONE_Red,

      hangxing_Blue,
      hangxing_Red,
      baopan_Blue,
      baopan_Red,
      KCXP_Blue,
      KCXP_Red,
      jiaohuanji_Blue,
      jiaohuanji_Red,
      zhongjianjian_Blue,
      zhongjianjian_Red,
      DB_Blue,
      DB_Red,
      serve_Blue,
      serve_Red,
      cipan_Blue,
      cipan_Red,

      /**
       * 图标
       */
      myChart: null,
      LineMap: new Map(),
      container: Math.random()
        .toString(36)
        .substr(2)
    }
  },
  mounted () {
    this.myChart = this.$echarts.init(document.getElementById(this.container))
    this.baseNode = getTuoPuNode(this.dataType) || []
    this.initChart()
  },
  props: {
    nodeDate: {
      required: false,
      default: () => [],
      type: Array
    },
    dataType: {
      required: false,
      default: '',
      type: String
    }
  },
  methods: {
    getIcon (type, status = 0) {
      let iconImage = ''
      if (type === 'frame' || type === 'relation') {
        iconImage = frameBG
      } else {
        let iconName = tyepDefault[type] + (status === 0 ? '_Blue' : '_Red')
        iconImage = this.$data[iconName]
      }
      return iconImage
    },
    initChart (onError = []) {
      let inErrorLine = new Map()
      let inErrorNode = []
      onError.length > 0 && onError.map(item => {
        if (item.state === 1 && item.belong === 'line') {
          let ErrorLine = inErrorLine.has(item.from) ? inErrorLine.get(item.from) : []
          ErrorLine.push(item.to)
          inErrorLine.set(item.from, ErrorLine)
        }
        if (item.state === 1 && item.belong === 'device') {
          inErrorNode.push(item.name)
        }
      })
      let nodes = JSON.parse(JSON.stringify(this.baseNode))
      /**
       * 对象说明：
       *    errorLine:错误状态线段数据
       *    edgeSymbolLine：带箭头线段（form——可能是拐点节点，to——必定是带图片的节点）
       *    singLine：单独的线段
       */
      let charts = {
        nodes: [],
        errorLine: [],
        linesData: [],
        edgeSymbolLine: [],
        singLine: []
      }
      let maindNodeMap = new Map()
      let nodeMap = new Map()
      let theSymbolSize = 64
      let RowNum = nodes.filter(nodeItem => {
        return nodeItem.type === 'frame'
      })
      let backgroundColor = 'transparent'
      /**
         * 节点上下接触点label
         */
      let nullLabel = {
        show: false
      }
      for (var j = 0; j < nodes.length; j++) {
        /**
         * 遍历节点数据  生成node 和 line 数据
         */
        const {x, y, name, type, line, width = 0, height = 0} = nodes[j]
        /**
         * 节点label
         */
        let baseLabel = {
          show: true,
          position: 'bottom',
          backgroundColor: backgroundColor,
          color: '#fff',
          fontSize: nodeFontSize,
          formatter: function (item) {
            return item.data.name
          }
        }
        /**
         * 层次label
         */
        let frameLabel = {
          show: true,
          position: 'bottom',
          backgroundColor: backgroundColor,
          formatter: function (item) {
            let val = item.data.name
            return `{x|${val}}`
          },
          rich: {
            x: {
              height: 25,
              width: 140,
              fontSize: 18,
              fontWeight: '500',
              align: 'center',
              color: '#e6f4ff',
              padding: [5, 10]
              // backgroundColor: {
              //   image: this.frameBG
              // }
            }
          }
        }
        /**
         * 关系label
         */
        let relationLabel = {
          show: false,
          position: 'bottom',
          backgroundColor: '#e6f4ff'
        }
        let node = {
          name: name,
          value: [x, y],
          symbolSize: type === 'frame' ? 0.001 : theSymbolSize,
          symbol: type === 'relation' ? 'rect' : 'image://' + this.getIcon(type, inErrorNode.includes(name) ? 1 : 0),
          itemStyle: {
            color: type === 'relation' ? backgroundColor : 'orange',
            backgroundColor: backgroundColor
          },
          label: type === 'frame' || type === 'relation' ? (type === 'frame' ? frameLabel : relationLabel) : baseLabel
        }
        if (type === 'relation') {
          node.symbolSize = [width, height]
          node.itemStyle = {
            color: '#3ba6fb',
            opacity: 0.2,
            backgroundColor: backgroundColor,
            shadowColor: '#3ba6fb',
            shadowBlur: 10,
            borderColor: '#3ba6fb',
            borderWidth: 0.5
          }
        }

        maindNodeMap.set(name, node)
        charts.nodes.push(node)
        let key = x + 'And' + y
        if (!nodeMap.has(key)) {
          nodeMap.set(key, node)
          /**
           * 设置节点的上下接触点
           */
          let nodeHeader = JSON.parse(JSON.stringify(node))
          nodeHeader.value[1] = nodeHeader.value[1] + (RowNum.length < 5 ? 55 : 45)
          nodeHeader.name = nodeHeader.name + 'header'
          nodeHeader.symbol = ''
          nodeHeader.symbolSize = 0.001
          nodeHeader.label = nullLabel
          maindNodeMap.set(name + 'header', nodeHeader)
          nodeMap.set(nodeHeader.value.join('And'), nodeHeader)
          charts.nodes.push(nodeHeader)

          let nodeLeft = JSON.parse(JSON.stringify(node))
          nodeLeft.value[0] = nodeLeft.value[0] + 45
          nodeLeft.name = nodeLeft.name + 'left'
          nodeLeft.symbol = ''
          nodeLeft.symbolSize = 0.001
          nodeLeft.label = nullLabel
          maindNodeMap.set(name + 'left', nodeLeft)
          nodeMap.set(nodeLeft.value.join('And'), nodeLeft)
          charts.nodes.push(nodeLeft)

          let nodeRight = JSON.parse(JSON.stringify(node))
          nodeRight.value[0] = nodeRight.value[0] - 45
          nodeRight.name = nodeRight.name + 'right'
          nodeRight.symbol = ''
          nodeRight.symbolSize = 0.001
          nodeRight.label = nullLabel
          maindNodeMap.set(name + 'right', nodeRight)
          nodeMap.set(nodeRight.value.join('And'), nodeRight)
          charts.nodes.push(nodeRight)

          let nodeBottom = JSON.parse(JSON.stringify(node))
          nodeBottom.value[1] = nodeBottom.value[1] - (RowNum.length < 5 ? 110 : 90)
          nodeBottom.name = nodeBottom.name + 'bottom'
          nodeHeader.symbol = ''
          nodeBottom.symbolSize = 0.001
          nodeBottom.label = nullLabel
          maindNodeMap.set(name + 'bottom', nodeBottom)
          nodeMap.set(nodeBottom.value.join('And'), nodeHeader)
          charts.nodes.push(nodeBottom)
        }
        if (line) {
          line.map(lineItem => {
            let lineItemErrorTarget = inErrorLine.get(name) || []
            let theLineStatus = lineItemErrorTarget.includes(lineItem.target) ? 1 : 0
            let item = {
              form: name,
              to: lineItem.target,
              status: theLineStatus,
              coords: lineItem.value
            }
            charts.linesData.push(item)
          })
        }
      }

      charts.linesData && charts.linesData.map(item => {
        let theCoords = JSON.parse(JSON.stringify(item.coords))
        let formNode = maindNodeMap.get(item.form)
        let toNode = maindNodeMap.get(item.to)
        let toNodeTill = {}
        let formNodeTill = JSON.parse(JSON.stringify(formNode))

        if (formNode.value[1] > toNode.value[1]) {
          formNode = maindNodeMap.get(item.form + 'bottom')
          toNodeTill = maindNodeMap.get(item.to + 'header')
        } else if (formNode.value[1] < toNode.value[1]) {
          formNode = maindNodeMap.get(item.form + 'header')
          toNodeTill = maindNodeMap.get(item.to + 'bottom')
        }
        if (theCoords.length === 0) {
          if (toNodeTill.value !== undefined)toNode = toNodeTill
        } else {
          if (theCoords[theCoords.length - 1][1] !== toNode.value[1]) {
            toNode = theCoords[theCoords.length - 1][1] > toNode.value[1] ? maindNodeMap.get(item.to + 'header') : maindNodeMap.get(item.to + 'bottom')
          }
          if (formNodeTill.value !== undefined && theCoords[0][1] === formNodeTill.value[1]) {
            formNode = formNodeTill
          } else if (formNodeTill.value !== undefined) {
            formNode = theCoords[0][1] > formNodeTill.value[1] ? maindNodeMap.get(item.form + 'header') : maindNodeMap.get(item.form + 'bottom')
          }
        }

        if (formNode && toNode) {
          item.coords.unshift(formNode.value)
          item.coords.push(toNode.value)
          let toNodeBase = maindNodeMap.get(item.to)
          let dataBase = JSON.parse(JSON.stringify(item.coords))
          if (dataBase.length > 2) {
            let beforEnd = dataBase[dataBase.length - 2].join('And')
            /**
             * 添加拐点节点，为了组装带箭头的线段数据
             */
            if (!nodeMap.has(dataBase[dataBase.length - 2].join('And'))) {
              let node = {
                name: '拐点' + beforEnd,
                value: dataBase[dataBase.length - 2],
                symbolSize: 0
              }
              nodeMap.set(beforEnd, node)
              charts.nodes.push(node)
            }
            let targetCoords = nodeMap.get(dataBase[dataBase.length - 2].join('And'))
            let endline = {
              source: targetCoords.name,
              target: targetCoords.value[1] === toNodeBase.value[1] ? toNodeBase.name : toNode.name
            }
            charts.edgeSymbolLine.push(endline)
            let singLine = JSON.parse(JSON.stringify(dataBase))
            if (singLine.length >= 3) {
              singLine.shift()
              singLine.pop()
              charts.singLine.push(singLine)
            }
          } else {
            let startline = {
              source: formNode.name,
              target: formNode.value[1] === toNodeBase.value[1] ? toNodeBase.name : toNode.name
            }
            if (formNode.value[1] === toNodeBase.value[1]) {
              startline = {
                source: formNode.value[0] > toNodeBase.value[0] ? formNode.name + 'right' : formNode.name + 'left',
                target: formNode.value[0] > toNodeBase.value[0] ? toNodeBase.name + 'left' : toNodeBase.name + 'right'
              }
            }
            charts.edgeSymbolLine.push(startline)
          }
          if (item.status !== 0) {
            /**
             * 根据线段数组判断开始节点的位置是否需要调整
             */
            if (dataBase[1] && (dataBase[1][1] === formNode.value[1] && dataBase[1][0] !== formNode.value[0])) {
              dataBase[0][0] = formNode.value[0] > dataBase[1][0] ? dataBase[0][0] - 50 : dataBase[0][0] + 50
            }
            if (dataBase[dataBase.length - 2] && dataBase[dataBase.length - 2][1] === toNodeBase.value[1] && dataBase[dataBase.length - 1][1] !== toNodeBase.value[1]) {
              if (dataBase[dataBase.length - 2][0] > toNodeBase.value[0]) {
                dataBase[dataBase.length - 1][0] = dataBase[dataBase.length - 1][0] + 50
              } else {
                dataBase[dataBase.length - 1][0] = dataBase[dataBase.length - 1][0] - 50
              }
            }
            charts.errorLine.push(dataBase)
          }
        }
      })

      let option = {
        backgroundColor: backgroundColor,
        grid: {
          left: '8%',
          top: '2%',
          right: '8%',
          bottom: '55',
          containLabel: true
        },
        xAxis: {
          min: 0,
          max: 1100,
          show: false,
          type: 'value'
        },
        yAxis: {
          min: 0,
          max: 1100,
          show: false,
          type: 'value'
        },
        series: [
          {
            type: 'graph',
            coordinateSystem: 'cartesian2d',
            edgeSymbol: [null, 'arrow'],
            edgeSymbolSize: [4, 10],
            edgeLabel: {
              normal: {
                textStyle: {
                  color: '#1ea6fb',
                  fontSize: 18
                }
              }
            },
            lineStyle: {
              width: 2,
              opacity: 1,
              color: '#1ea6fb'
            },
            data: charts.nodes,
            links: charts.edgeSymbolLine
          },
          {
            type: 'lines',
            polyline: true,
            coordinateSystem: 'cartesian2d',
            lineStyle: {
              width: 2,
              opacity: 1,
              color: '#1ea6fb'
              // curveness: 0.3
            },
            data: charts.singLine
          },
          {
            type: 'lines',
            polyline: true,
            coordinateSystem: 'cartesian2d',
            lineStyle: {
              width: 3,
              opacity: 1,
              color: 'red',
              curveness: 0.3
            },
            effect: {
              show: true,
              trailLength: 0,
              symbol: 'arrow',
              color: 'red',
              symbolSize: 8
            },
            data: charts.errorLine
          }]
      }
      this.myChart.setOption(option, true)
    }
  }
}
</script>

<style scoped>
.echartsTuoPu{
  width: 100%;
  height: 100%;
}
</style>



