<template>
  <section class="chartsBox">
    <div :id="container" class="container" ref="reference"></div>
  </section>
</template>

<script>
// 图表基础引用
export default {
  name: "polarAreaMap",
  data() {
    return {
      sourDate: null,
      myChart: null,
      container: Math.random()
        .toString(36)
        .substr(2)
    };
  },
  props: {
    ChartSour: {
      required: false,
      default: () => [],
      type: Array
    }
  },
  watch: {
    ChartSour: {
      handler(newName, oldName) {
        if (newName.length > 0) {
          let _this = this;
          this.$nextTick(() => {
            _this.sourDate = JSON.parse(JSON.stringify(newName));
            _this.initChart(_this.sourDate);
          });
        }
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    this.myChart = this.$echarts.init(document.getElementById(this.container));
  },
  methods: {
    initChart(ChartSour) {
      let color = ["#3ab9fe", "#ff536e", "#feb33f", "#26f6b8", "#ed84ec"];
      let sourdata = [];
      let unit = "";
      ChartSour.forEach(element => {
        unit = element.unit;
        let item = {
          value: element.value,
          name: element.indicateName ? element.indicateName : element.name
        };
        sourdata.push(item);
      });
      let option = {
        color: color,
        backgroundColor: "transparent",
        title: {
          text: "",
          left: "center",
          top: "50%",
          textStyle: {
            fontSize: 22,
            color: "#e0e8ff",
            fontWeight: "normal"
          }
        },
        tooltip: {
          trigger: "item"
        },
        legend: false,
        series: [
          {
            type: "pie",
            roseType: "radius",
            radius: ["15%", "60%"],
            data: sourdata,
            label: {
              normal: {
                formatter:
                  "{font|{b}}\n{hr|}\n{num|{c}}" + "{num|" + unit + "}",
                rich: {
                  font: {
                    fontSize: 14,
                    padding: [5, 0],
                    color: "#6ecaff"
                  },
                  num: {
                    fontSize: 14,
                    fontWeight: "bold",
                    padding: [5, 0],
                    color: "#e0e8ff"
                  },
                  hr: {
                    height: 0,
                    borderWidth: 1,
                    width: "100%",
                    borderColor: "#e0e8ff"
                  }
                }
              }
            },
            labelLine: {
              lineStyle: {
                color: "#e0e8ff"
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0,0,0,0.5)"
              }
            }
          }
        ]
      };
      this.myChart.setOption(option);
    }
  }
};
</script>

<style scoped>
.container {
  width: 100%;
  height: 300px;
}
</style>



