<script>
import websockObject from "@/mixins/websock";
import backgroundImage from "@/assets/DailyOperation/background/2-2.jpg";
import warningBox from "./com/warningBox";
import statusInfo from "./com/statusInfo";
export default {
  name: "cneter",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage,
      // eslint-disable-next-line no-undef
      titleList: dailyCenterHeader.centerList,
      /**
       * 测试机房
       */
      jigui: [],
      electricDate: [],
      airConditionerStatus: [],

      /**
       * 配线间
       */
      jiguiRight: [],
      electricDateRight: [],
      airConditionerStatusRight: []
    };
  },
  components: {
    warningBox,
    statusInfo
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.dailyCenterCenter.websockUrl;
    this.useTheTestDate();
  },
  methods: {
    useTheTestDate() {
      let tillTiem = new Date().getTime();
      let m = 10000;
      let n = 0;
      let test = [
        /**
         * 测试机房
         * @temperature 温度
         * @humidity 湿度
         *
         * @group 19F测试机房 or 19F测试机房电柜
         */
        {
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜1",
          group: "19F测试机房"
        },
        {
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜2",
          group: "19F测试机房"
        },
        {
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜" + Math.floor(Math.random() * (10 - 1) + 1),
          group: "19F测试机房"
        },
        {
          dateTime: tillTiem,
          value: [
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName: "机房PUE"
            },

            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            }
          ],
          group: "19F测试机房电柜"
        },
        /**
         * 19F门禁、空调运行状态
         * @status 0：正常, 1:异常
         * @type
         *  1.air ：空调
         *  2.water ：漏水检测情况
         *  3.UPS ：UPS
         *  4.door  ：门禁
         *  5.wind  :抽风机
         * @group 19F门禁、空调运行状态
         */
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "air",
          indicateName: "机房精密空调B",
          group: "19F门禁空调运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "water",
          indicateName: "漏水检测情况",
          group: "19F门禁空调运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "UPS",
          indicateName: "UPS",
          group: "19F门禁空调运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "door",
          indicateName: "机房门禁A",
          group: "19F门禁空调运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "wind",
          indicateName: "抽风机",
          group: "19F门禁空调运行状态"
        },

        /**
         * 19F-25F配线间，同上
         *
         */
        {
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜1",
          group: "19F至25F配线间"
        },
        {
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜2",
          group: "19F至25F配线间"
        },
        {
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜" + Math.floor(Math.random() * (100 - 1) + 1),
          group: "19F至25F配线间"
        },
        {
          dateTime: tillTiem,
          value: [
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName:
                "电柜电压" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "kw",
              type: "电压",
              indicateName: "机房PUE"
            },

            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            },
            {
              value: Math.floor(Math.random() * (m - n) + n),
              unit: "A",
              type: "电流",
              indicateName:
                "电柜电流" + Math.floor(Math.random() * (10 - 1) + 1)
            }
          ],
          group: "19F至25F配线间电柜"
        },
        /**
         *
         * @status 0：正常, 1:异常
         * @type
         *  1.air ：空调
         *  2.water ：漏水检测情况
         *  3.UPS ：UPS
         *  4.door  ：门禁
         *  5.wind  :抽风机
         * @group 19F门禁、空调运行状态
         */
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "air",
          indicateName: "机房精密空调B",
          group: "19F至25F门禁抽风机运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "water",
          indicateName: "漏水检测情况",
          group: "19F至25F门禁抽风机运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "UPS",
          indicateName: "UPS",
          group: "19F至25F门禁抽风机运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "door",
          indicateName: "机房门禁A" + Math.floor(Math.random() * (100 - 1) + 1),
          group: "19F至25F门禁抽风机运行状态"
        },
        {
          dateTime: tillTiem,
          value: Math.random() > 0.5 ? 0 : 1,
          type: "wind",
          indicateName: "抽风机" + Math.floor(Math.random() * (100 - 1) + 1),
          group: "19F至25F门禁抽风机运行状态"
        }
      ];
      for (let index = 0; index < 20; index++) {
        test.push({
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜" + Math.floor(Math.random() * (100 - 1) + 1),
          group: "19F测试机房"
        });

        test.push({
          dateTime: tillTiem,
          temperature: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          humidity: {
            unit: "℃",
            value: Math.floor(Math.random() * (m - n) + n)
          },
          indicateName: "机柜" + Math.floor(Math.random() * (100 - 1) + 1),
          group: "19F至25F配线间"
        });
      }
      this.getWebsocketMsg(test);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          if (element.group === this.titleList[0].group) {
            self.toJiGui(element, this.jigui, 1);
          }
          if (element.group === this.titleList[1].group) {
            let base = JSON.parse(JSON.stringify(element));
            let baseSour = [];
            let typeIndex = new Map();
            base.value.forEach(item => {
              let target = typeIndex.has(item.type)
                ? typeIndex.get(item.type)
                : baseSour.length;
              let itemList = baseSour[target] || [];
              itemList.push(item);
              baseSour[target] = itemList;
              typeIndex.set(item.type, target);
            });
            self.electricDate = baseSour;
          }
          if (element.group === this.titleList[2].group) {
            self.toJiGui(element, this.airConditionerStatus, 2);
          }

          if (element.group === this.titleList[3].group) {
            self.toJiGui(element, this.jiguiRight, 3);
          }
          if (element.group === this.titleList[4].group) {
            self.electricDateRight = element.value;
          }
          if (element.group === this.titleList[5].group) {
            self.toJiGui(element, this.airConditionerStatusRight, 5);
          }
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    toJiGui(sour, history, type) {
      let historyDate = JSON.parse(JSON.stringify(history));
      let target = historyDate.findIndex(item => {
        return item.indicateName === sour.indicateName;
      });
      if (target === -1) {
        historyDate.push(sour);
      } else {
        historyDate[target] = sour;
      }
      switch (type) {
        case 1:
          this.jigui = historyDate;
          break;
        case 2:
          this.airConditionerStatus = historyDate;
          break;
        case 3:
          this.jiguiRight = historyDate;
          break;
        case 5:
          this.airConditionerStatusRight = historyDate;
          break;
        default:
          console.log("toJiGui");
          break;
      }
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="contentBox">
          <section class="leftContent">
            <section class="mainChartBox eventTable contentMain">
              <div class="title">{this.titleList[0].title}</div>
              <warningBox
                sourDate={this.jigui}
                contentHeight={280}
                class="warningMainBox"
                runnring={true}
                overflow="hidden"
              >
                <div class="warningType" slot="tabeleheader">
                  名称
                </div>
                <div class="warningName" slot="tabeleheader">
                  温度
                </div>
                <div class="warningInfo" slot="tabeleheader">
                  湿度
                </div>
              </warningBox>
              <section class="footerBox borderRight">
                {this.electricDate.length > 0 &&
                  this.electricDate.map(electricDateitem => {
                    return (
                      <section class="footerBoxItem">
                        <warningBox
                          sourDate={electricDateitem}
                          contentHeight={167}
                          runnring={false}
                          overflow="hidden"
                        >
                          <div class="warningType" slot="tabeleheader">
                            名称
                          </div>
                          <div class="warningName" slot="tabeleheader">
                            电压
                          </div>
                        </warningBox>
                      </section>
                    );
                  })}
              </section>
            </section>
            <section class="mainChartBox contentMain statusMain">
              <div class="title">{this.titleList[2].title}</div>
              <statusInfo
                sourDate={this.airConditionerStatus}
                contentHeight={315}
              ></statusInfo>
            </section>
          </section>
          <section class="rightContent">
            <section class="mainChartBox eventTable contentMain">
              <div class="title">{this.titleList[3].title}</div>
              <section class="footerBox">
                <warningBox
                  sourDate={this.jiguiRight}
                  contentHeight={590}
                  class="footerBoxItem warningMainBox"
                  runnring={true}
                  overflow="hidden"
                  {...{ style: { paddingLeft: 0 } }}
                >
                  <div class="warningType" slot="tabeleheader">
                    名称
                  </div>
                  <div class="warningName" slot="tabeleheader">
                    温度
                  </div>
                  <div class="warningName" slot="tabeleheader">
                    湿度
                  </div>
                </warningBox>
                <warningBox
                  sourDate={this.electricDateRight}
                  contentHeight={590}
                  class="footerBoxItem warningMainBox"
                  runnring={true}
                  overflow="hidden"
                >
                  <div class="warningType" slot="tabeleheader">
                    名称
                  </div>
                  <div class="warningName" slot="tabeleheader">
                    电压/电流
                  </div>
                </warningBox>
              </section>
            </section>
            <section class="mainChartBox contentMain statusMain">
              <div class="title">{this.titleList[5].title}</div>
              <statusInfo
                sourDate={this.airConditionerStatusRight}
                contentHeight={210}
              ></statusInfo>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.contentBox {
  width: 1805px;
  height: 1010px;
  padding: 25px;
  position: absolute;
  left: 35px;
  top: 10px;
  display: flex;
}
.leftContent {
  width: 735px;
  height: 100%;
}
.rightContent {
  width: 900px;
  flex: auto;
  height: 100%;
}

.mainChartBox .title {
  width: 100%;
  font-size: 24px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: 0px;
  color: #e0e8ff;
  left: 15px;
  top: 0;
  text-align: left;
}
.contentMain {
  padding-top: 45px !important;
}
.statusMain {
  margin-top: 25px;
}

.warningMainBox {
  padding: 0 15px;
}
.jiguiCon {
  height: 280px;
  overflow: auto;
}
.jiguiConRight {
  height: 590px;
  overflow: auto;
}
.electricCon {
  height: 167px;
  overflow: auto;
}
.footerBox {
  display: flex;
  padding: 0 15px;
}
.footerBoxItem {
  width: 50%;
  position: relative;
}
.borderRight .footerBoxItem:first-child::after {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: 99;
}
.warningName {
  position: relative;
}
.warningName:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: -1;
}
.warningName:before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: -1;
}
</style>



