<template>
  <div class="ZBQHLineChart" :id="container"></div>
</template>

<script>
import echarts from 'echarts'
export default {
  name: 'ZBQHLineChart',
  data () {
    return {
      charDateX: [],
      charDateY: [],
      limit: 0,
      myChart: null,
      container: Math.random()
        .toString(36)
        .substr(2)
    }
  },
  props: {
    bgColor: {
      required: false,
      default: () => {
        return {
          color: '#5DE5FF',
          color1: 'rgba(93,229,255,0.7)',
          color2: 'rgba(93,229,255,0.1)'
        }
      },
      type: Object
    },
    chartData: {
      required: false,
      default: () => [],
      type: Array
    }
  },
  mounted () {
    this.charDateX = []
    this.charDateY = []
    this.myChart = this.$echarts.init(document.getElementById(this.container))
    this.initChart()
  },
  methods: {
    initChart () {
      let option = {
        title: {
          text: this.chartData[0] && this.chartData[0].title,
          x: 'left',
          textStyle: {
            fontWeight: 400,
            color: '#70baff',
            fontSize: 20
          }
        },
        tooltip: {
          trigger: 'axis',
          padding: [2, 10],
          formatter: '{b} : {c}',
          textStyle: {
            fontSize: 16
          }
        },
        grid: {
          x: 70, // 数值
          y: 50,
          x2: 50, // 周
          y2: 70
        },
        xAxis: {
          splitLine: {show: false}, // 去除网格线
          type: 'category',
          data: this.chartData.map(item => item.time),
          axisLine: {
            lineStyle: {
              width: 1,
              color: '#70baff'
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: ['rgba(18, 32, 142, 0)', 'rgba(18, 32, 142, 0.3)']
            }
          },
          axisLabel: {
            // interval: 0, // 强制文字产生间隔
            textStyle: { // 文字样式
              fontSize: 12,
              fontFamily: 'Microsoft YaHei'
            }
          }
        },
        yAxis: {
          type: 'value',
          name: '',
          nameTextStyle: {
            color: '#707070',
            fontSize: 20
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: 'dashed',
              color: '#39498C'
            }
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              width: 1,
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0,
                  color: '#70baff'
                }, {
                  offset: 1,
                  color: '#70baff'
                }],
                globalCoord: false // 缺省为 false
              }
            }
          },
          axisLabel: {
            color: '#70baff',
            fontWeight: 400,
            formatter: '{value}'
          }
        },
        series: [{
          type: 'line',
          symbol: 'circle',
          symbolSize: 1,
          smooth: true,
          animationDuration: 2000,
          itemStyle: {
            normal: {
              color: this.bgColor.color
            }
          },
          lineStyle: {
            normal: {
              width: 2
            }
          },
          areaStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: this.bgColor.color1
              },
              {
                offset: 1,
                color: this.bgColor.color2
              }
              ], false),
              shadowBlur: 20
            }
          },
          data: this.chartData.map(item => item.value)
        }]
      }
      this.myChart.setOption(option)
    }
  },
  watch: {
    chartData: {
      handler (newName, oldName) {
        if (newName.length > 0 && this.myChart) {
          this.initChart()
        }
      },
      immediate: true,
      deep: true
    }
  }
}
</script>

<style scoped>

</style>



