<script>
import Overview1 from "@/assets/DailyOperation/shucai/v2_Overview1.png";
import Overview2 from "@/assets/DailyOperation/shucai/v2_Overview2.png";
import Overview3 from "@/assets/DailyOperation/shucai/v2_Overview3.png";
export default {
  name: "borTurning",
  data() {
    return {
      Overview1,
      Overview2,
      Overview3
    };
  },
  props: {
    sourDate: {
      required: false,
      default: () => ({}),
      type: Object
    },
    index: {
      required: false,
      default: 0,
      type: Number
    }
  },
  computed: {
    showImage() {
      return this.index % 3;
    }
  },
  render(h) {
    return (
      <section class="borTurning">
        <section class="borTurnBox">
          <div class="borTurnValue">
            <img
              src={
                this.showImage === 0
                  ? this.Overview1
                  : this.showImage === 1
                  ? this.Overview2
                  : this.Overview3
              }
              class="running"
            />
            <span class="mainFont">{this.sourDate.value}</span>
          </div>
          <div class="borTurnName">
            {this.sourDate.indicateName}
            {this.sourDate.unit === "" ? "" : "(" + this.sourDate.unit + ")"}
          </div>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.borTurning {
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.borTurnBox {
  width: 121px;
}
.borTurnValue {
  position: relative;
  width: 121px;
  height: 121px;
}
.mainFont {
  width: 100%;
  text-align: center;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  font-size: 24px;
  font-weight: bold;
}
.borTurnName {
  width: 121px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #a3e0ff;
}
@keyframes mymove {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}

@-webkit-keyframes mymove /*Safari and Chrome*/ {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
.running {
  position: absolute;
  width: 121px;
  animation: mymove 5s infinite;
  -webkit-animation: mymove 5s infinite; /*Safari and Chrome*/
  animation-timing-function: linear;
  top: 0;
  left: 0;
  z-index: 1;
}
</style>



