<template>
  <section class="LineChartLimit">
    <div class="Charttitle">
      {{title}}
    </div>
    <section class="ChartCon" :id="container"></section>
  </section>
</template>

<script>
import echarts from 'echarts'
export default {
  name: 'LineChartLimit',
  data () {
    return {
      charDateX: [],
      charDateY: [],
      initChartObject: null,
      limit: 0,
      option: null,
      myChart: null,
      container: Math.random()
        .toString(36)
        .substr(2)
    }
  },
  mounted () {
    this.charDateX = []
    this.charDateY = []
    this.myChart = this.$echarts.init(document.getElementById(this.container))
    this.initChart()
  },
  props: {
    sourDate: {
      required: false,
      default: () => [],
      type: Array
    },
    title: {
      required: false,
      default: '',
      type: String
    }
  },
  watch: {
    sourDate: {
      handler (newName, oldName) {
        if (newName !== undefined && newName.length > 0) {
          /**
           * 节流渲染
           */
          let _this = this
          this.$nextTick(() => {
            _this.charDateX = []
            _this.charDateY = []
            let theLimit = 0
            theLimit = newName[0].limit
            newName.map(item => {
              _this.charDateX.push(item.value)
              _this.charDateY.push(_this.dateFormat(new Date(Number(item.dateTime)), 'yyyy-MM-dd hh:mm:ss'))
            })
            _this.limit = theLimit === 0 ? Math.max(..._this.charDateX) * 0.8 : theLimit
            _this.initChartObject && clearTimeout(_this.initChartObject)
            _this.initChartObject = setTimeout(() => {
              _this.initChart()
            }, 1000)
          })
        }
      },
      immediate: true,
      deep: true
    }
  },
  beforeDestroy () {
    this.initChartObject && clearTimeout(this.initChartObject)
  },
  methods: {
    dateFormat (date, fmt) {
      const o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds(),
        'q+': Math.floor((date.getMonth() + 3) / 3),
        S: date.getMilliseconds()
      }
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
      }
      for (let k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
          fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length))
        }
      }
      return fmt
    },
    initChart () {
      this.option = {
        title: {
          text: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          padding: [2, 10],
          textStyle: {
            fontSize: 16
          }
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              width: 1,
              color: '#70baff'
            }
          },
          axisTick: {
            show: true,
            alignWithLabel: true,
            lineStyle: {
              width: 3
            }
          },
          axisLabel: {
            rotate: 45,
            color: '#70baff',
            fontWeight: 400,
            formatter: function (value) {
              return value.split(' ')[1]
            }
          },
          data: this.charDateY
        },
        grid: {
          left: '2%',
          top: '35px',
          right: '4%',
          bottom: '4%',
          containLabel: true
        },
        visualMap: [{
          show: false,
          pieces: [{
            gt: 0,
            lte: this.limit * 0.6,
            color: '#31f7bd'
          }, {
            gt: this.limit * 0.6,
            lte: this.limit,
            color: 'yellow'
          }, {
            gt: this.limit,
            color: 'red'
          }],
          seriesIndex: 0
        }],
        yAxis: {
          type: 'value',
          name: '',
          nameTextStyle: {
            color: '#707070', fontSize: 20
          },
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              width: 1,
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0,
                  color: '#70baff'
                }, {
                  offset: 1,
                  color: '#70baff'
                }],
                globalCoord: false // 缺省为 false
              }
            }
          },
          axisTick: {
            lineStyle: {
              width: 0.5
            }
          },
          axisLabel: {
            color: '#70baff',
            fontWeight: 400
          }
        },
        series: [{
          type: 'line',
          symbol: 'circle',
          symbolSize: 1,
          smooth: true,
          animationDuration: 2000,
          itemStyle: {
            normal: {
              color: '#00ca95'
            }
          },
          lineStyle: {
            normal: {
              opacity: 1,
              width: 2
            }
          },
          areaStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: 'rgba(0,202,149,0.3)'
              },
              {
                offset: 1,
                color: 'rgba(0,202,149,0)'
              }
              ], false),
              shadowColor: 'rgba(0,202,149, 0.9)',
              shadowBlur: 20
            }
          },
          data: this.charDateX
        }]
      }
      this.myChart.setOption(this.option)
    }
  }
}
</script>

<style scoped>
.LineChartLimit{
  position: relative;
}
.Charttitle{
  position: absolute;
  color: #e0e8ff;
  width: 100%;
  top: 0;
  left: 2%;
  text-align: left;
}
.ChartCon{
  width: 100%;
  height: 100%;
}
</style>



