<template>
  <div style="position: relative;overflow: hident;float: right;">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage1 + ')' }"
    ></section>
    <div class="BigContent">
      <div class="content weatherTime">
        <div style="text-align:right;color: white;">
          <h1 style="font-size: 78px;height: 103px">
            <!-- <span>32℃</span>
              <span>晴</span> -->
          </h1>
          <h1 style="font-size: 70px;">{{ date }} {{ time }}</h1>
        </div>
      </div>
      <div class="content content1">
        <div style="width: 1750px;margin: auto;">
          <header class="header mainTitile">零售关键数据折线图</header>
          <div class="orderArray" style="height: 600;overflow: hident">
            <template
              v-for="(value, index) in sourceData['零售关键数据折线图']"
            >
              <ZBQHLineChart
                :key="index"
                :chartData="value"
                :bgColor="{
                  color: '#50FA5A',
                  color1: 'rgba(80,250,90,0.7)',
                  color2: 'rgba(80,250,90,0.1)'
                }"
                style="width: 580px;height: 330px;"
              ></ZBQHLineChart>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import backgroundImage1 from "@/assets/zaiBeiQieHuan/3-1.jpg";
import ZBQHLineChart from "@/components/ZBQHLineChart";
import websockObject from "@/mixins/websock";
import { right1Data } from "../../../static/zaibeiRightData";
export default {
  name: "Right1",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage1,
      sourceData: {},
      interval: null,
      date: "",
      time: ""
    };
  },
  components: {
    ZBQHLineChart
  },
  created() {
    this.sourceData = {};
    right1Data.forEach(item => {
      let groupList = {};
      item.data.forEach(elem => {
        groupList[elem] = [];
      });
      this.sourceData[item.group] = groupList;
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.rightWebsockUrl;
    // this.useTheTestDate()
    this.setTime();
  },
  methods: {
    setTime() {
      this.date = new Date()
        .toLocaleDateString()
        .split("/")
        .join("-");
      this.time = new Date().toTimeString().split(" ")[0];
      this.interval = setInterval(() => {
        this.time = new Date().toTimeString().split(" ")[0];
      }, 1000);
    },
    getWebsocketMsg(sourceData) {
      if (sourceData.length > 0) {
        this.$nextTick(() => {
          this.transformtionData(sourceData);
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      this.timeOut = setInterval(() => {
        let tillNow = new Date().getTime();
        const sourceData = [
          {
            dateTime: tillNow,
            value: parseInt(Math.random() * 100),
            title: "零售关键数据折线图",
            group: "零售关键数据折线图",
            indicateName: "委托笔数数据",
            time: tillNow
          },
          {
            dateTime: tillNow,
            value: parseInt(Math.random() * 100),
            title: "零售关键数据折线图",
            group: "零售关键数据折线图",
            indicateName: "成交笔数数据",
            time: tillNow
          },
          {
            dateTime: tillNow,
            value: parseInt(Math.random() * 100),
            title: "零售关键数据折线图",
            group: "零售关键数据折线图",
            indicateName: "开户业务数据",
            time: tillNow
          },
          {
            dateTime: tillNow,
            value: parseInt(Math.random() * 100),
            title: "零售关键数据折线图",
            group: "零售关键数据折线图",
            indicateName: "银证转账数据",
            time: tillNow
          },
          {
            dateTime: tillNow,
            value: parseInt(Math.random() * 100),
            title: "零售关键数据折线图",
            group: "零售关键数据折线图",
            indicateName: "中登流水数据",
            time: tillNow
          },
          {
            dateTime: tillNow,
            value: parseInt(Math.random() * 100),
            title: "零售关键数据折线图",
            group: "零售关键数据折线图",
            indicateName: "操作流水数据",
            time: tillNow
          }
        ];
        this.transformtionData(sourceData);
      }, 2000);
    },
    transformtionData(data) {
      let groupList = right1Data.map(item => item.group);
      groupList.forEach(item => {
        const group = data.filter(elem => elem.group === item);
        group.forEach(element => {
          if (
            this.sourceData[item] &&
            this.sourceData[item][element.indicateName]
          ) {
            let dataList = JSON.parse(
              JSON.stringify(this.sourceData[item][element.indicateName])
            );
            if (
              this.sourceData[item][element.indicateName] &&
              this.sourceData[item][element.indicateName].length === 10
            ) {
              console.log(dataList);
              dataList.shift();
            }
            dataList.push({
              time: new Date(parseInt(element.time))
                .toTimeString()
                .split(" ")[0]
                .split(":")
                .splice(0, 2)
                .join(":"),
              value: element.value,
              title: element.indicateName
            });
            this.$set(this.sourceData[item], element.indicateName, dataList);
          }
        });
      });
      this.$forceUpdate();
    }
  },
  beforeDestroy() {
    clearInterval(this.interval);
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.content {
  width: 1835px;
  margin-left: 20px;
  overflow: hidden;
}
.weatherTime {
  height: 300px;
}
.header {
  width: 1750px;
  margin: auto;
  text-align: left;
  font-size: 36px;
  height: 100px;
  line-height: 100px;
}
.orderArray {
  position: relative;
  overflow: auto;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
.content1 {
  height: 720px;
  margin-top: 18px;
}
.content2 {
  height: 1420px;
}
.content3 {
  height: 720px;
}
</style>



