<template>
  <section class="lineChart">
    <div class="title">
      {{limtTitle}}
    </div>
    <div :id="container" class="container" ref="reference"></div>
  </section>
</template>

<script>
// 图表基础引用
import ChartBase from '@/mixins/ChartBase'
import { Chart } from '@antv/g2'
export default {
  mixins: [ChartBase],
  name: 'singleLineChart',
  data () {
    return {
      limtTitle: '',
      legendLength: 0,
      xStyle: {
        fontSize: 14,
        textAlign: 'center',
        textBaseline: 'middle',
        fill: '#6ecaff'
      },
      yStyle: {
        fontSize: 14,
        fill: '#6ecaff'
      },
      lineStyle: {
        fill: '#3bc1ff',
        fillOpacity: 0.7,
        stroke: '#3bc1ff',
        lineWidth: 1,
        strokeOpacity: 0.7,
        endArrow: true
      }
    }
  },
  props: {
    sourDate: {
      required: false,
      default: () => [],
      type: Array
    },
    chartsHeight: {
      required: false,
      default: 200,
      type: Number
    }
  },
  watch: {
    sourDate: {
      handler (newName, oldName) {
        if (newName !== undefined && newName.length > 0) {
          let max = newName.reduce((num1, num2) => {
            return num1.value > num2.value ? num1.value : num2.value
          })
          if (max > 100000) {
            this.limtTitle = '(万元)'
            let base = JSON.parse(JSON.stringify(newName))
            this.charDate = base.map(item => {
              item.value = item.value / 10000
              return item
            })
          } else {
            this.limtTitle = ''
            this.charDate = newName
          }
          let _this = this
          this.$nextTick(() => {
            _this.updatedCharts()
          })
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    createdChart () {
      try {
        this.chartOb.clear()
      } catch (e) {
        // console.log('尝试清空图表')
      }
      try {
        if (this.charDate.length > 0) {
          this.chartOb = new Chart({
            container: this.$refs.reference,
            padding: [15, 45, 35, 45],
            autoFit: true,
            height: this.chartsHeight
          })
          let sour = JSON.parse(JSON.stringify(this.charDate))
          this.chartOb.data(sour)
          let legendItem = []
          let newsMap = new Map()
          sour.map(item => {
            newsMap.set(item.indicateName, item.value)
            if (!legendItem.includes(item.indicateName)) {
              legendItem.push(item.indicateName)
            }
          })
          this.legendLength = legendItem.length
          this.chartOb.axis('value', {
            grid: null,
            label: {
              autoRotate: false,
              offset: 5,
              rotate: 0,
              style: {
                fontSize: 14,
                fill: '#6ecaff'
              },
              formatter: value => {
                return value
              }
            },
            line: {
              style: this.lineStyle
            }
          })
          this.chartOb.axis('dateTime', {
            grid: null,
            label: {
              offset: 15,
              style: {
                fontSize: 14,
                textAlign: 'center',
                textBaseline: 'middle',
                fill: '#6ecaff'
              }
            },
            line: {
              style: this.lineStyle
            }
          })
          this.chartOb.scale('dateTime', {
            type: 'time',
            tickCount: 5,
            mask: 'MM:DD'
          })
          this.chartOb.scale('value', {
            min: 0,
            tickCount: 5
          })
          this.chartOb.tooltip({
            showCrosshairs: true,
            shared: true
          })
          let colors = [
            'l(270) 0:#fe9844 1:#ffe535',
            'l(270) 0:#26f6b8 1:#26f6b8',
            'l(270) 0:#5586ff 1:#37d7ff'
          ]
          this.chartOb.legend(false)
          this.chartOb
            .area()
            .position('dateTime*value')
            .color('indicateName', colors)
            .shape('smooth')
          this.chartOb
            .line()
            .position('dateTime*value')
            .color('indicateName', colors)
            .shape('smooth')

          this.chartOb.interaction('active-region')
          this.chartOb.render()
        } else {

        }
      } catch (e) {
        console.error(JSON.stringify(e))
      }
    }
  }
}
</script>

<style scoped>
.container {
  width: auto;
  background: transparent;
  margin: 10px auto;
  position: relative;
}
.lineChart{
  position: relative;
}
.title{
  position: absolute;
  z-index: 99;
  color: #6ecaff;
  font-size: 16px;
  top: 0;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 20px;
}
</style>



