<template>
  <div>
    <div style="width: 280px">
      <p style="height: 40px;line-height: 40px;font-size: 20px;">
        <span style="float: left;color: #cee4ff;">{{data.title}}</span>
        <span style="float: right;font-size: 24px;color: #ffee5d;font-weight: bold;">{{ data.value }}%</span>
      </p>
      <div style="width: 280px;background: #15287d;border-radius: 10px;overflow: hidden;">
        <div class="progress" :style="{ backgroundImage: bgColor, width: `${data.value}%` }"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'progressbra',
  props: {
    bgColor: {
      required: false,
      default: 'linear-gradient(-90deg, #01d8fe 0%,#03a8e9 50%,#0478d4 100%)'
    },
    data: {
      required: false
    }
  }
}
</script>

<style scoped>
.progress {
  height: 20px;
  border-radius: 10px;
  background-blend-mode: normal, normal;
  transition-duration: 1s;
}
</style>



