            this.cpuTop = element.value;
            // self.toTop(element, this.cpuTop, 1)
          }
          if (element.group === this.cloudList[10].group) {
            element.value = element.value.map(item => {
              item.indicateName = item.name;
              item.group = item.name;
              return item;
            });
            this.usingTop = element.value;
            // self.toTop(element, this.usingTop, 2)
          }
        });
        if (craeterDateBase.length > 0) {
          self.craeterDate = craeterDateBase;
        }
        if (corderDateBase.length > 0) {
          self.orderDate = corderDateBase;
        }
        if (cloudDateOneBase.length > 0) {
          self.toCloudDateCharts(cloudDateOneBase, 1);
        }
        if (cloudDateTwoBase.length > 0) {
          self.toCloudDateCharts(cloudDateTwoBase, 2);
        }
        if (cloudDateThreeBase.length > 0) {
          self.toCloudDateCharts(cloudDateThreeBase, 3);
        }
        if (cloudDateFourBase.length > 0) {
          self.toCloudDateCharts(cloudDateFourBase, 4);
        }
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    toTop(sour, history, type) {
      let historyDate = JSON.parse(JSON.stringify(history));
      let target = historyDate.findIndex(item => {
        return item.indicateName === sour.indicateName;
      });
      if (target === -1) {
        historyDate.push(sour);
      } else {
        historyDate[target] = sour;
      }
      switch (type) {
        case 1:
          this.cpuTop = historyDate;
          break;
        case 2:
          this.usingTop = historyDate;
          break;
        default:
          console.error("toCloudDate error");
          break;
      }
    },
    toCloudDateCharts(sour, type) {
      switch (type) {
        case 1:
          this.$set(this.cloudDateOne, "chartsList", sour);
          break;
        case 2:
          this.$set(this.cloudDateTwo, "chartsList", sour);
          break;
        case 3:
          this.$set(this.cloudDateThree, "chartsList", sour);
          break;
        case 4:
          this.$set(this.cloudDateFour, "chartsList", sour);
          break;
        default:
          console.error("toCloudDate error");
          break;
      }
    },
    toCloudDate(sour, history, type) {
      let normalList = history.normalList;
      if (sour.type === "normal") {
        let target = normalList.findIndex(item => {
          return item.indicateName === sour.indicateName;
        });
        if (target === -1) {
          normalList.push(sour);
        } else {
          normalList[target] = sour;
        }
      }
      switch (type) {
        case 1:
          this.$set(this.cloudDateOne, "normalList", normalList);
          break;
        case 2:
          this.$set(this.cloudDateTwo, "normalList", normalList);
          break;
        case 3:
          this.$set(this.cloudDateThree, "normalList", normalList);
          break;
        case 4:
          this.$set(this.cloudDateFour, "normalList", normalList);
          break;
        default:
          console.error("toCloudDate error");
          break;
      }
    },
    toCostDisplay(sour) {
      this.costDisplay = sour.value;
    },
    toPrivateDate(sour) {
      let historyDate = JSON.parse(JSON.stringify(this.privateDate));
      let target = historyDate.findIndex(item => {
        return item.indicateName === sour.indicateName;
      });
      if (target === -1) {
        historyDate.push(sour);
        this.privateDate = historyDate.sort((a, b) => {
          return this.privateOrder.indexOf(a.indicateName) >
            this.privateOrder.indexOf(b.indicateName)
            ? 1
            : -1;
        });
      } else {
        this.$set(this.privateDate, target, sour);
      }
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="contentBox">
          <section class="contentItem">
            <section class="mainChartBox">
              <div class="title">{this.cloudList[0].title}</div>
              <div class="privateBox contentMain">
                {this.privateDate.map((item, index) => {
                  return (
                    <section
                      class="privateItem"
                      {...{ style: { order: index } }}
                    >
                      <borTurning sourDate={item} index={index}></borTurning>
                    </section>
                  );
                })}
              </div>
            </section>

            <section class="mainChartBox usedPie">
              <div class="title">{this.cloudList[1].title}</div>
              <costPie ChartSour={this.costDisplay}></costPie>
            </section>

            <section class="mainChartBox eventTable contentMain">
              <div class="title">{this.cloudList[2].title}</div>
              <section class="warningBox">
                <section
                  class="warningItem warningItemHeader"
                  {...{
                    style: { backgroundImage: " url(" + this.tabelHeader + ")" }
                  }}
                >
                  <div class="warningType">资源类型</div>
                  <div class="warningName">资源名</div>
                  <div class="warningInfo">告警信息</div>
                </section>
                <section class="warningContent">
                  <vue-seamless-scroll
                    class="seamless-warp"
                    class-option={this.classOption}
                    data={this.warningList.map(item => {
                      return {
                        title: item.type,
                        date: item.name
                      };
                    })}
                    {...{
                      style: { height: "172px", overflow: "hidden" }
                    }}
                  >
                    <ul class="item">
                      {this.warningList.map((item, index) => {
                        return (
                          <li
                            class="warningItem"
                            {...{
                              style: {
                                backgroundImage:
                                  " url(" +
                                  (index % 2 === 0
                                    ? this.tabelHeader1
                                    : this.tabelHeader2) +
                                  ")"
                              }
                            }}
                          >
                            <span class="warningType">{item.type}</span>
                            <span class="warningName">{item.name}</span>
                            <span class="warningInfo">{item.alertMsg}</span>
                          </li>
                        );
                      })}
                    </ul>
                  </vue-seamless-scroll>
                </section>
              </section>
            </section>
          </section>
          <section class="contentItem">
            <section class="mainChartBox">
              <div class="title">{this.cloudList[3].title}</div>
              <div class="cloudBox contentMain">
                <cloudCharts sourDate={this.cloudDateOne}></cloudCharts>
              </div>
            </section>
            <section class="mainChartBox">
              <div class="title">{this.cloudList[4].title}</div>
              <div class="cloudBox contentMain">
                <cloudCharts sourDate={this.cloudDateTwo}></cloudCharts>
              </div>
            </section>
            <section class="footerBox">
              <section class="mainChartBox footerBoxItem">
                <div class="title">{this.cloudList[7].title}</div>
                <div class="cloudBox contentMain">
                  <singleLineChart
                    sourDate={this.craeterDate}
                  ></singleLineChart>
                </div>
              </section>
              <section class="mainChartBox footerBoxItem">
                <div class="title">{this.cloudList[8].title}</div>
                <div class="cloudBox contentMain">
                  <singleLineChart sourDate={this.orderDate}></singleLineChart>
                </div>
              </section>
            </section>
          </section>
          <section class="contentItem">
            <section class="mainChartBox">
              <div class="title">{this.cloudList[5].title}</div>
              <div class="cloudBox contentMain">
                <cloudCharts sourDate={this.cloudDateThree}></cloudCharts>
              </div>
            </section>
            <section class="mainChartBox">
              <div class="title">{this.cloudList[6].title}</div>
              <div class="cloudBox contentMain">
                <cloudCharts sourDate={this.cloudDateFour}></cloudCharts>
              </div>
            </section>
            <section class="footerBox">
              <section class="mainChartBox footerBoxItem">
                <div class="title">{this.cloudList[9].title}</div>
                <div class="cloudBox contentMain">
                  <singleInterval sourDate={this.cpuTop}></singleInterval>
                </div>
              </section>
              <section class="mainChartBox footerBoxItem">
                <div class="title">{this.cloudList[10].title}</div>
                <div class="cloudBox contentMain">
                  <singleInterval sourDate={this.usingTop}></singleInterval>
                </div>
              </section>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.mainChartBox {
  display: flex;
  flex-flow: column;
  padding-bottom: 20px;
}
.eventTable {
  height: 260px;
}
.usedPie {
  padding-top: 25px;
  height: auto;
}
.contentBox {
  width: 1805px;
  height: 1010px;
  padding: 25px;
  position: absolute;
  left: 35px;
  top: 10px;
  display: flex;
}
.contentItem {
  width: 33.3%;
  height: 100%;
}
.mainChartBox .title {
  width: 100%;
  font-size: 22px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: 0px;
  color: #e0e8ff;
  left: 0;
  top: 0;
  text-align: left;
}
.contentMain {
  padding-top: 35px !important;
}
.privateBox {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  height: 300px;
}
.privateItem {
  width: 25%;
}
.privateItem:nth-child(1),
.privateItem:nth-child(2),
.privateItem:nth-child(3) {
  width: 33.3%;
}

.warningItem {
  width: 100%;
  height: 43px;
  display: flex;
  align-items: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.warningType {
  width: 150px;
}
.warningName {
  width: 150px;
}
.warningInfo {
  width: 300px;
}
.warningContent {
  height: 172px;
  overflow: auto;
}
.warningContent .warningName {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border-right: 1px solid #2153ca;
  border-left: 1px solid #2153ca; */
}
.warningName {
  position: relative;
}
.warningName:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: -1;
}
.warningName:before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: -1;
}
.warningBox {
  padding: 0 15px;
}
.footerBox {
  display: flex;
}
.footerBoxItem {
  width: 50%;
}
</style>



