<template>
  <div
    style="background: #122fc2;"
    :class="{
      errorBorder: getErrorBorderList[`${room}/${rank}/${title}`] === false,
      okBorder:
        getErrorBorderList[`${room}/${rank}/${title}`] === true ||
        getErrorBorderList[`${room}/${rank}/${title}`] === undefined
    }"
  >
    <div class="rowFooterTitle">{{ title }}</div>
    <div class="rowFooterContent">
      <template v-for="(item, index) in dataList">
        <div
          :key="index"
          :style="{
            lineHeight: item.length > 10 ? 'normal' : '50px',
            backgroundImage: `url('${bgImgMethod(item)}')`
          }"
          :class="{ isMarginAuto: isMarginAuto }"
        >
          {{ item }}
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import v1redGif from "./../assets/v1_red.gif";
import v1bluepng from "./../assets/v1_blue.png";
import v1Green from "./../assets/v1_green.png";
export default {
  name: "footerContent",
  props: {
    dataList: {
      required: false,
      default: () => [],
      type: Array
    },
    room: {
      required: false
    },
    rank: {
      required: false
    },
    title: {
      required: false
    },
    isMarginAuto: {
      required: false,
      default: false
    }
  },
  data() {
    return {
      data: []
    };
  },
  computed: {
    ...mapGetters(["getErrorDeviceList", "getErrorBorderList"])
  },
  mounted() {},
  methods: {
    bgImgMethod(name) {
      if (
        this.getErrorDeviceList[
          `${this.room}/${this.rank}/${this.title}/${name}`
        ] === undefined
      ) {
        return v1bluepng;
      } else if (
        this.getErrorDeviceList[
          `${this.room}/${this.rank}/${this.title}/${name}`
        ]
      ) {
        return v1Green;
      } else {
        return v1redGif;
      }
    }
  }
};
</script>

<style scoped>
.rowFooterTitle {
  font-size: 25px;
  color: #daebff;
  width: 100%;
  height: 16px;
  line-height: 16px;
  padding: 20px;
  text-align: left;
}
.rowFooterContent {
  width: 100%;
  padding: 0 20px 20px 20px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.rowFooterContent > div {
  height: 50px;
  width: 190px;
  font-size: 25px;
  line-height: 50px;
  color: #daebff;
  background-size: 100% 100%;
  margin-top: 10px;
}
.isMarginAuto {
  margin: auto;
}
.errorBorder {
  border: 2px solid red;
  border-radius: 10px;
  box-sizing: content-box;
}
.okBorder {
  border: 2px solid #4bc6ff;
  border-radius: 10px;
  box-sizing: content-box;
}
</style>



