<script>
import tabelHeader from "@/assets/DailyOperation/shucai/v1_table1.png";
import tabelHeader1 from "@/assets/DailyOperation/shucai/v1_table2.png";
import tabelHeader2 from "@/assets/DailyOperation/shucai/v1_table3.png";
export default {
  name: "warningBox",
  data() {
    return {
      tabelHeader,
      tabelHeader1,
      tabelHeader2
    };
  },
  props: {
    sourDate: {
      required: false,
      default: () => [],
      type: Array
    },
    contentHeight: {
      required: false,
      default: 500,
      type: Number
    },
    overflow: {
      required: false,
      default: "auto",
      type: String
    },
    runnring: {
      required: false,
      default: false,
      type: Boolean
    }
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        singleHeight: 43,
        waitTime: 2500
      };
    }
  },
  render(h) {
    return (
      <section class="warningBox">
        <section
          class="warningItem warningItemHeader"
          {...{
            style: { backgroundImage: " url(" + this.tabelHeader + ")" }
          }}
        >
          {this.$slots.tabeleheader}
        </section>
        {this.runnring && this.sourDate.length > 0 ? (
          <vue-seamless-scroll
            class="seamless-warp"
            class-option={this.classOption}
            data={this.sourDate.map(item => {
              return {
                title: item.indicateName,
                date: item.dateTime
              };
            })}
            {...{
              style: {
                height: this.contentHeight + "px",
                overflow: this.overflow
              }
            }}
          >
            <ul class="item">
              {this.sourDate &&
                this.sourDate.map((item, index) => {
                  if (this.$slots.tabeleheader.length === 3) {
                    return (
                      <li
                        class="warningItem"
                        {...{
                          style: {
                            backgroundImage:
                              " url(" +
                              (index % 2 === 0
                                ? this.tabelHeader1
                                : this.tabelHeader2) +
                              ")"
                          }
                        }}
                      >
                        <span class="warningType">{item.indicateName}</span>
                        <span class="warningName">
                          {item.temperature.value}
                          {item.temperature.unit}
                        </span>
                        <span class="warningInfo">
                          {item.humidity.value}
                          {item.humidity.unit}
                        </span>
                      </li>
                    );
                  } else {
                    let base = JSON.parse(JSON.stringify(item.value));
                    if (Array.isArray(base)) {
                      return base.map(ele => (
                        <li
                          class="warningItem"
                          {...{
                            style: {
                              backgroundImage:
                                " url(" +
                                (index % 2 === 0
                                  ? this.tabelHeader1
                                  : this.tabelHeader2) +
                                ")"
                            }
                          }}
                        >
                          <span class="warningType">{ele.indicateName}</span>
                          <span class="warningName">
                            {ele.value}
                            {ele.unit ? ele.unit : ""}
                          </span>
                        </li>
                      ));
                    } else {
                      return (
                        <li
                          class="warningItem"
                          {...{
                            style: {
                              backgroundImage:
                                " url(" +
                                (index % 2 === 0
                                  ? this.tabelHeader1
                                  : this.tabelHeader2) +
                                ")"
                            }
                          }}
                        >
                          <span class="warningType">{item.indicateName}</span>
                          <span class="warningName">
                            {item.value}
                            {item.unit ? item.unit : ""}
                          </span>
                        </li>
                      );
                    }
                  }
                })}
            </ul>
          </vue-seamless-scroll>
        ) : (
          <section
            class="warningContent"
            {...{
              style: {
                height: this.contentHeight + "px",
                overflow: this.overflow
              }
            }}
          >
            {this.sourDate &&
              this.sourDate.map((item, index) => {
                if (this.$slots.tabeleheader.length === 3) {
                  return (
                    <section
                      class="warningItem"
                      {...{
                        style: {
                          backgroundImage:
                            " url(" +
                            (index % 2 === 0
                              ? this.tabelHeader1
                              : this.tabelHeader2) +
                            ")"
                        }
                      }}
                    >
                      <div class="warningType">{item.indicateName}</div>
                      <div class="warningName">
                        {item.temperature.value}
                        {item.temperature.unit}
                      </div>
                      <div class="warningInfo">
                        {item.humidity.value}
                        {item.humidity.unit}
                      </div>
                    </section>
                  );
                } else {
                  let base = JSON.parse(JSON.stringify(item.value));
                  if (Array.isArray(base)) {
                    return base.map(ele => (
                      <section
                        class="warningItem"
                        {...{
                          style: {
                            backgroundImage:
                              " url(" +
                              (index % 2 === 0
                                ? this.tabelHeader1
                                : this.tabelHeader2) +
                              ")"
                          }
                        }}
                      >
                        <div class="warningType">{ele.indicateName}</div>
                        <div class="warningName">
                          {ele.value}
                          {ele.unit ? ele.unit : ""}
                        </div>
                      </section>
                    ));
                  } else {
                    return (
                      <section
                        class="warningItem"
                        {...{
                          style: {
                            backgroundImage:
                              " url(" +
                              (index % 2 === 0
                                ? this.tabelHeader1
                                : this.tabelHeader2) +
                              ")"
                          }
                        }}
                      >
                        <div class="warningType">{item.indicateName}</div>
                        <div class="warningName">
                          {item.value}
                          {item.unit ? item.unit : ""}
                        </div>
                      </section>
                    );
                  }
                }
              })}
          </section>
        )}
      </section>
    );
  }
};
</script>

<style scoped>
ul {
  margin: 0;
  padding: 0;
}
.warningItem {
  width: 100%;
  height: 43px;
  display: flex;
  align-items: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.warningType {
  width: 50%;
}
.warningName {
  width: 50%;
}
.warningInfo {
  width: 50%;
}
.warningContent {
  height: 506px;
  overflow: auto;
}
.electricContent {
  height: 300px;
  overflow: auto;
}
.usingLeftContent {
  height: 635px;
  overflow: auto;
}
.warningContent .warningName {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid #2153ca;
  border-left: 1px solid #2153ca;
}
.warningBox {
}
.Carousel_li {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.warningName {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.warningName:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: -1;
}
.warningName:before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 1px;
  height: 100%;
  background: linear-gradient(5deg, #23b7cb, #2153ca, #23b7cb);
  z-index: -1;
}
</style>



