<script>
import airblue from "@/assets/DailyOperation/shucai/status/v1_air_blue.png";
import airred from "@/assets/DailyOperation/shucai/status/v1_air_red.png";

import doorblue from "@/assets/DailyOperation/shucai/status/v1_door_blue.png";
import doorred from "@/assets/DailyOperation/shucai/status/v1_door_red.png";

import UPSblue from "@/assets/DailyOperation/shucai/status/v1_UPS_blue.png";
import UPSred from "@/assets/DailyOperation/shucai/status/v1_UPS_red.png";

import windblue from "@/assets/DailyOperation/shucai/status/v1_wind_blue.png";
import windred from "@/assets/DailyOperation/shucai/status/v1_wind_red.png";

import waterblue from "@/assets/DailyOperation/shucai/status/v1_water_blue.png";
import waterred from "@/assets/DailyOperation/shucai/status/v1_water_red.png";
export default {
  name: "statusInfo",
  data() {
    return {
      airblue,
      airred,
      doorblue,
      doorred,
      UPSblue,
      UPSred,
      windblue,
      windred,
      waterblue,
      waterred
    };
  },
  props: {
    sourDate: {
      required: false,
      default: () => [],
      type: Array
    },
    contentHeight: {
      required: false,
      default: 500,
      type: Number
    }
  },
  methods: {
    getImage(item) {
      let back = "";
      /**
       *  1.air ：空调
       *  2.water ：漏水检测情况
       *  3.UPS ：UPS
       *  4.door  ：门禁
       *  5.wind  :抽风机
       */
      let type = item.booleType ? item.booleType : item.type;
      switch (type) {
        case "air":
          back = item.value === 0 ? this.airblue : this.airred;
          break;
        case "water":
          back = item.value === 0 ? this.waterblue : this.waterred;
          break;
        case "UPS":
          back = item.value === 0 ? this.UPSblue : this.UPSred;
          break;
        case "door":
          back = item.value === 0 ? this.doorblue : this.doorred;
          break;
        case "wind":
          back = item.value === 0 ? this.windblue : this.windred;
          break;
        default:
          break;
      }
      return back;
    }
  },
  render(h) {
    return (
      <section
        class="statusInfo"
        {...{
          style: { height: this.contentHeight + "px" }
        }}
      >
        {this.sourDate.length > 0 &&
          this.sourDate.map(item => {
            return (
              <div class="statusItem">
                <img
                  src={this.getImage(item)}
                  class={item.status === 0 ? "statusIcon" : "statusIcon error"}
                />
                <div class="statusName">{item.indicateName}</div>
              </div>
            );
          })}
      </section>
    );
  }
};
</script>

<style scoped>
.statusInfo {
  height: 310px;
  overflow: auto;
  display: flex;
  flex-wrap: wrap;
}
.statusItem {
  width: 120px;
  height: 95px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  margin-top: 10px;
}
.statusIcon {
  display: block;
  width: 68px;
  height: 68px;
}
.statusName {
  padding-top: 5px;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 1px;
  color: #80b0fe;
}
</style>



