                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="bigContent" id="bigContent" style="position: absolute;width: 5500px;top: 94px;left: 155px;height: 2765px;"></div>
      </section>
    </div>
</template>
<script>
import backgroundImage from '@/assets/zaiBeiQieHuan/2-1.jpg'
import LineChartLimit from '@/components/LineChartLimit'
import ProgressBar from '@/components/ProgressBar'
import FooterContent from './components/FooterContent'
import websockObject from '@/mixins/websock'
import zaibeiEquipment from '../../../static/zaibeiEquipment'
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: 'ZBQHLeftHeader',
  mixins: [websockObject],
  data () {
    return {
      backgroundImage,
      myChart: null,
      zaibeiEquipment,
      linesData: [],
      linesErrorData: []
    }
  },
  computed: {
    ...mapGetters(['getErrorDeviceList', 'getErrorBorderList'])
  },
  components: {
    LineChartLimit,
    ProgressBar,
    FooterContent
  },
  mounted () {
    this.wsuri = ''
    this.myChart = this.$echarts.init(document.getElementById('bigContent'))
    this.initChart()
    this.test()
  },
  methods: {
    ...mapMutations(['setErrorDeviceList', 'setErrorBorderList']),
    initChart () {
      const fiexd = [
        { spot: 320, top: 2365, differ: 20, rank: '渠道外联层', title: '员工工作平台' },
        { spot: 930, top: 2365, differ: 40, rank: '渠道外联层', title: '零售客户终端' },
        { spot: 1330, top: 2365, differ: 60, rank: '渠道外联层', title: '外联接入' },
        { spot: 1630, top: 2365, differ: 80, rank: '渠道外联层', title: '报盘接入' },

        { spot: 875, top: 1932, differ: 20, rank: '总线/接入层', title: '总线接入' },
        { spot: 1235, top: 1932, differ: 40, rank: '总线/接入层', title: '二级接入' },
        { spot: 1600, top: 1932, differ: 60, rank: '总线/接入层', title: '统一认证' },

        { spot: 330, top: 1432, differ: 15, rank: '业务应用非交易', title: '风险控制' },
        { spot: 890, top: 1432, differ: 30, rank: '业务应用非交易', title: '运营支撑' },
        { spot: 1215, top: 1432, differ: 45, rank: '业务应用非交易', title: '集中运营' },
        { spot: 1440, top: 1432, differ: 60, rank: '业务应用非交易', title: '营销服务' },
        { spot: 1670, top: 1432, differ: 75, rank: '业务应用非交易', title: '资讯管理' },

        { spot: 210, top: 937, differ: 10, rank: '业务应用交易', title: '零售经纪/财富管理' },
        { spot: 540, top: 937, differ: 20, rank: '业务应用交易', title: '机构经纪' },
        { spot: 770, top: 937, differ: 30, rank: '业务应用交易', title: '资产管理' },
        { spot: 1000, top: 937, differ: 40, rank: '业务应用交易', title: '投行' },
        { spot: 1330, top: 937, differ: 50, rank: '业务应用交易', title: '自营' },
        { spot: 1670, top: 937, differ: 60, rank: '业务应用交易', title: '快订量化' },

        { spot: 440, top: 529, differ: 30, rank: '业务应用企业数据', title: '企业数据平台' },

        { spot: 140, top: 30, differ: 5, rank: '公共服务', title: '产品中心' },
        { spot: 430, top: 30, differ: 15, rank: '公共服务', title: '支付中心' },
        { spot: 730, top: 30, differ: 25, rank: '公共服务', title: '清算中心' }
      ]
      this.linesData = []
      this.linesErrorData = []
      fiexd.forEach(item => {
        let long = 1860
        let spot1 = item.spot
        let spot2 = long + spot1
        let spot3 = long * 2 + spot1 + 50
        let top = item.top
        let differ = item.differ
        this.linesData.push({ coords: [[spot1, top], [spot1, top - differ], [spot2, top - differ], [spot2, top]], name: `东莞机房/${item.rank}/${item.title}/深圳机房/${item.rank}/${item.title}` })
        this.linesData.push({ coords: [[spot1, top], [spot1, top - differ], [spot3, top - differ], [spot3, top]], name: `东莞机房/${item.rank}/${item.title}/上海机房/${item.rank}/${item.title}` })

        this.linesData.push({ coords: [[spot2, top], [spot2, top - differ], [spot1, top - differ], [spot1, top]], name: `深圳机房/${item.rank}/${item.title}/东莞机房/${item.rank}/${item.title}` })
        this.linesData.push({ coords: [[spot2, top], [spot2, top - differ], [spot3, top - differ], [spot3, top]], name: `深圳机房/${item.rank}/${item.title}/上海机房/${item.rank}/${item.title}` })

        this.linesData.push({ coords: [[spot3, top], [spot3, top - differ], [spot1, top - differ], [spot1, top]], name: `上海机房/${item.rank}/${item.title}/东莞机房/${item.rank}/${item.title}` })
        this.linesData.push({ coords: [[spot3, top], [spot3, top - differ], [spot2, top - differ], [spot2, top]], name: `上海机房/${item.rank}/${item.title}/深圳机房/${item.rank}/${item.title}` })
      })
      this.setChartOption()
    },
    setChartOption () {
      let option = {
        grid: { left: 'left', bottom: 0, width: '100%', height: '100%' },
        xAxis: { min: 0, max: 5530, show: false, type: 'value' },
        yAxis: { min: 0, max: 2765, show: false, type: 'value' },
        series: [{
          type: 'lines',
          polyline: true,
          coordinateSystem: 'cartesian2d',
          lineStyle: { type: 'solid', width: 4, color: '#75efff', curveness: 0.3 },
          effect: { show: true, trailLength: 0, symbol: 'circle', color: '#95EAF7', symbolSize: 20, period: 10 },
          data: this.linesData
        }, {
          type: 'lines',
          polyline: true,
          coordinateSystem: 'cartesian2d',
          lineStyle: { type: 'solid', width: 4, color: '#FF0000', curveness: 0.1, opacity: 1 },
          effect: { show: true, trailLength: 0, symbol: 'circle', color: '#FF0000', symbolSize: 20, period: 10 },
          data: this.linesErrorData
        }]
      }
      this.myChart.setOption(option)
    },
    getWebsocketMsg (sourDate) {
      if (sourDate.length > 0) {
        this.transformtionData(sourDate)
      }
      return new Promise(function (resolve, reject) {
        resolve()
      })
    },
    useTheTestDate () {
      const data = []
      if (this.myChart) {
        this.transformtionData(data)
      }
    },
    transformtionData (data) {
      const device = data.filter(item => item.type === 'device')
      let errorDeviceList = JSON.parse(JSON.stringify(this.getErrorDeviceList))
      let errorBorderList = JSON.parse(JSON.stringify(this.getErrorBorderList))
      device.forEach(item => {
        // 0 高亮（正常） 1 暗淡（异常）
        // deviceState[`${item.room}/${item.rank}/${item.title}/${item.name}`] = !item.value
        errorDeviceList[`${item.room}/${item.rank}/${item.title}/${item.name}`] = !item.value
      })
      const line = data.filter(item => item.type === 'line')
      line.forEach(item => {
        let name = `${item.form.room}/${item.form.rank}/${item.form.title}/${item.to.room}/${item.to.rank}/${item.to.title}`
        errorBorderList[`${item.form.room}/${item.form.rank}/${item.form.title}`] = !item.value
        errorBorderList[`${item.to.room}/${item.to.rank}/${item.to.title}`] = !item.value
        if (!item.value) {
          const index = this.linesErrorData.findIndex(elem => elem.name === name)
          if (index >= 0) {
            const data = this.linesErrorData.splice(index, 1)
            this.linesData.push(...data)
          }
        } else {
          const index = this.linesData.findIndex(elem => elem.name === name)
          if (index >= 0) {
            const data = this.linesData.splice(index, 1)
            this.linesErrorData.push(...data)
          }
        }
      })
      this.setErrorDeviceList(errorDeviceList)
      this.setErrorBorderList(errorBorderList)
      this.setChartOption()
    },
    test () {
      let testData = [
        {
          type: 'line',
          dateTime: 1599623630795,
          value: 1,
          form: {
            room: '东莞机房',
            rank: '渠道外联层',
            title: '员工工作平台',
            name: 'AD系统'
          },
          to: {
            room: '上海机房',
            rank: '渠道外联层',
            title: '员工工作平台',
            name: 'AD系统'
          }
        }
      ]
      Object.keys(zaibeiEquipment).forEach(rank => {
        Object.keys(zaibeiEquipment[rank]).forEach(title => {
          zaibeiEquipment[rank][title].forEach(name => {
            testData.push({ room: '东莞机房', type: 'device', value: 0, rank, title, name })
          })
        })
      })
      testData.push(
        {
          room: '东莞机房',
          type: 'device',
          dateTime: 1599623630795,
          value: 1,
          rank: '渠道外联层',
          title: '员工工作平台',
          name: 'AD系统'
        },
        {
          room: '上海机房',
          type: 'device',
          dateTime: 1599623630795,
          value: 1,
          rank: '渠道外联层',
          title: '员工工作平台',
          name: '文件云'
        }
      )
      this.transformtionData(testData)
    },
    test2 () {
      const data = [
        {
          room: '东莞机房',
          type: 'device',
          dateTime: 1599623630795,
          value: 0,
          rank: '渠道外联层',
          title: '零售客户终端',
          name: '易通达信'
        },
        {
          room: '东莞机房',
          type: 'device',
          dateTime: 1599623630795,
          value: 1,
          rank: '总线/接入层',
          title: '总线接入',
          name: 'KESB总线'
        },
        {
          type: 'line',
          dateTime: 1599623630795,
          value: 0,
          form: {
            room: '东莞机房',
            rank: '渠道外联层',
            title: '员工工作平台',
            name: 'AD系统'
          },
          to: {
            room: '深圳机房',
            rank: '渠道外联层',
            title: '员工工作平台',
            name: 'AD系统'
          }
        }
      ]
      this.transformtionData(data)
    }
  }
}
</script>

<style scoped>
.mainChartBox{
  width: 5760px;
  height: 3240px;
  overflow: hidden;
}
.bigContent {
  margin-top: 320px;
  width: 100%;
  height: 2860px;
  font-size: 0;
}
.content {
  height: 2860px;
  overflow: hidden;
  display: inline-block;
}
.content1 {
  width: 1850px;
}
.content2 {
  width: 1920px;
}
.content3 {
  width: 1850px;
}
.ZBQHTitle {
  width: 100%;
  height: 54px;
  font-size: 48px;
  text-align: center;
}
.content1Left .content1Right {
  height: 2860px;
  overflow: hidden;
}
.content1Left {
  font-size: 20px;
  width: 60px;
  text-align: start;
  float: left;
}
.content1Right {
  width: 1780px;
  display: inline-block;
  padding-left: 20px;
  box-sizing: border-box;
}
.textCertical {
  writing-mode: vertical-lr;
  display: inline-block;
  width: 100%;
  text-align: center;
  padding: 13px;
  height: 200px;
  background-color: #212caf;
  border: solid 1px #3c9cff;
  box-sizing: border-box;
}
.content1Left > div {
  width: 100%;
}
.rowTop {
  float:left;
  position: relative
}
.rowHeader {
  height: 44px;
  background: #122fc2;
  font-size: 30px;
  line-height: 44px;
  color: #daebff
}
.rowFooter {
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap
}
.rowBottom {
  float:right;
  position: relative
}
</style>



