<template>
  <div :class="{'FlipClock':clockSize==='big','smallClock':clockSize!=='big'}">
    <Flipper ref="flipperYear1" :clockSize='clockSize'/>
    <Flipper ref="flipperYear2" :clockSize='clockSize'/>
    <Flipper ref="flipperYear3" :clockSize='clockSize'/>
    <Flipper ref="flipperYear4" :clockSize='clockSize'/>
    <em>-</em>
    <Flipper ref="flipperMouth1" :clockSize='clockSize'/>
    <Flipper ref="flipperMouth2" :clockSize='clockSize'/>
    <em>-</em>
    <Flipper ref="flipperDay1" :clockSize='clockSize'/>
    <Flipper ref="flipperDay2" :clockSize='clockSize'/>
      <em style="width:50px;">   </em>
    <Flipper ref="flipperHour1" :clockSize='clockSize'/>
    <Flipper ref="flipperHour2" :clockSize='clockSize'/>
    <em>:</em>
    <Flipper ref="flipperMinute1" :clockSize='clockSize'/>
    <Flipper ref="flipperMinute2" :clockSize='clockSize'/>
    <em>:</em>
    <Flipper ref="flipperSecond1" :clockSize='clockSize'/>
    <Flipper ref="flipperSecond2" :clockSize='clockSize'/>
  </div>
</template>

<script>
import Flipper from './Flipper'

export default {
  name: 'FlipClock',
  data () {
    return {
      timer: null,
      dayObjs: [],
      flipObjs: []
    }
  },
  props: {
    clockSize: {
      required: false,
      default: 'big',
      type: String
    }
  },
  components: {
    Flipper
  },
  beforeDestroy () {
    if (JSON.stringify(this.timer) !== null) {
      window.clearInterval(this.timer)
    }
  },
  methods: {
    // 初始化数字
    init () {
      let now = new Date()
      let nowTimeStr = this.formatDate(new Date(now.getTime()), 'hhiiss')
      let nowDateTimeStr = this.formatDate(new Date(now.getTime()), 'yyyymmdd')
      for (let i = 0; i < this.dayObjs.length; i++) {
        this.dayObjs[i].setFront(nowDateTimeStr[i])
      }
      for (let i = 0; i < this.flipObjs.length; i++) {
        this.flipObjs[i].setFront(nowTimeStr[i])
      }
    },
    // 开始计时
    run () {
      this.timer = setInterval(() => {
        // 获取当前时间
        let now = new Date()

        let nowDateTimeStr = this.formatDate(new Date(now.getTime()), 'yyyymmdd')
        for (let i = 0; i < this.dayObjs.length; i++) {
          this.dayObjs[i].setFront(nowDateTimeStr[i])
        }

        let nowTimeStr = this.formatDate(new Date(now.getTime() - 1000), 'hhiiss')
        let nextTimeStr = this.formatDate(now, 'hhiiss')
        for (let i = 0; i < this.flipObjs.length; i++) {
          if (nowTimeStr[i] === nextTimeStr[i]) {
            continue
          }
          this.flipObjs[i].flipDown(
            nowTimeStr[i],
            nextTimeStr[i]
          )
        }
      }, 1000)
    },
    // 正则格式化日期
    formatDate (date, dateFormat) {
      /* 单独格式化年份，根据y的字符数量输出年份
     * 例如：yyyy => 2019
            yy => 19
            y => 9
     */
      if (/(y+)/.test(dateFormat)) {
        dateFormat = dateFormat.replace(
          RegExp.$1,
          (date.getFullYear() + '').substr(4 - RegExp.$1.length)
        )
      }
      // 格式化月、日、时、分、秒
      let o = {
        'm+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'i+': date.getMinutes(),
        's+': date.getSeconds()
      }
      for (let k in o) {
        if (new RegExp(`(${k})`).test(dateFormat)) {
          // 取出对应的值
          let str = o[k] + ''
          /* 根据设置的格式，输出对应的字符
           * 例如: 早上8时，hh => 08，h => 8
           * 但是，当数字>=10时，无论格式为一位还是多位，不做截取，这是与年份格式化不一致的地方
           * 例如: 下午15时，hh => 15, h => 15
           */
          dateFormat = dateFormat.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : this.padLeftZero(str)
          )
        }
      }
      return dateFormat
    },
    // 日期时间补零
    padLeftZero (str) {
      return ('00' + str).substr(str.length)
    }
  },
  mounted () {
    this.dayObjs = [
      this.$refs.flipperYear1,
      this.$refs.flipperYear2,
      this.$refs.flipperYear3,
      this.$refs.flipperYear4,
      this.$refs.flipperMouth1,
      this.$refs.flipperMouth2,
      this.$refs.flipperDay1,
      this.$refs.flipperDay2
    ]
    this.flipObjs = [
      this.$refs.flipperHour1,
      this.$refs.flipperHour2,
      this.$refs.flipperMinute1,
      this.$refs.flipperMinute2,
      this.$refs.flipperSecond1,
      this.$refs.flipperSecond2
    ]
    this.init()
    this.run()
  }
}
</script>

<style scoped>
.FlipClock {
    text-align: center;
    color: #63e7ff;
}
.FlipClock .M-Flipper {
    margin: 0 3px;
}
.FlipClock em {
    display: inline-block;
    line-height: 102px;
    font-size: 66px;
    font-style: normal;
    vertical-align: top;
}
.smallClock {
    text-align: center;
    color: #fff;
}
.smallClock .M-Flipper {
    margin: 0 3px;
}
.smallClock em {
    display: inline-block;
    line-height: 65px;
    font-size: 40px;
    font-style: normal;
    vertical-align: top;
}
</style>



