<template>
  <div class="transChartsBar">
    <div class="bgContent" :style="{ height: bgContentHeight }">
      <div class="playMiddle">
        <div class="contentData" :style="{height: height}">
          <div class="contentDataTitle">凤岗</div>
          <div class="contentDataRight" :style="{height: height}">
            <div class="contentDataRightAbsolu">
              <div class="contentDataRightChartDiv">
                <div class="contentDataRightChart"  :style="{backgroundImage: 'linear-gradient(-90deg, #00b1ff 0%,#0e6fe8 50%,#0e6fe8 100%)', width: `${sourceData['凤岗'].percValue}%`}"></div>
                <div class="contentDataRightNumber" :style="{left: `${sourceData['凤岗'].percValue}%`}">{{sourceData['凤岗'].value}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="contentData" :style="{height: height}">
          <div class="contentDataTitle">后海</div>
          <div class="contentDataRight" :style="{height: height}">
            <div class="contentDataRightAbsolu">
              <div class="contentDataRightChartDiv">
                <div class="contentDataRightChart" :style="{backgroundImage: 'linear-gradient(-90deg, #06ff87 0%,#06ff87 50%,#09ec4f 100%)', width: `${sourceData['后海'].percValue}%`}"></div>
                <div class="contentDataRightNumber" :style="{left: `${sourceData['后海'].percValue}%`}">{{sourceData['后海'].value}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="contentData" :style="{height: height}">
          <div class="contentDataTitle">金桥</div>
          <div class="contentDataRight" :style="{height: height}">
            <div class="contentDataRightAbsolu">
              <div class="contentDataRightChartDiv">
                <div class="contentDataRightChart" :style="{backgroundImage: 'linear-gradient(-90deg, #f5e933 0%,#f5e933 50%,#f8b617 100%)', width: `${sourceData['金桥'].percValue}%`}"></div>
                <div class="contentDataRightNumber" :style="{left: `${sourceData['金桥'].percValue}%`}">{{sourceData['金桥'].value}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
let sourceData = { 凤岗: {}, 后海: {}, 金桥: {} }
export default {
  name: 'transChartsBar',
  props: {
    bgContentHeight: {
      required: false
    },
    height: {
      required: false
    },
    sourceData: {
      required: false,
      default: () => sourceData,
      type: Object
    }
  }
}
</script>
<style scoped>

.transChartsBar .bgContent {
  width: 100%;
  position: relative
}
.transChartsBar .playMiddle {
  position: absolute;
  width: 100%;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%)
}
.transChartsBar .contentDataRight {
  width:90%;
  left: 10%;
  ;border-left: 2px solid #323a9b;
}
.transChartsBar .contentDataRightAbsolu {
  width: 90%;
  height: 20px;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
}
.transChartsBar .contentDataRightChartDiv {
  max-width: 80%;
  height: 20px;
  text-align: left;
  position: relative;
}
.transChartsBar .contentDataRightChart {
  height: 20px;
  border-radius: 0 10px 10px 0;
  display: inline-block;
}
.transChartsBar .contentDataRightNumber {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 80%;
  margin-left: 10px;
}
.contentData {
  width: 100%;
  position: relative;
}
.contentData .contentDataTitle {
  height: 20px;
  display: inline-block;
  width: 10%;
  left: 0;
  color: #cee4ff;
}

.contentData > div {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}
</style>



