<template>
  <div style="position: relative;overflow: hident;float: right;">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage3 + ')' }"
    ></section>
    <div class="BigContent">
      <div class="content content2">
        <div style="width: 1750px;margin:20px auto;">
          <div class="orderArray" style="height: 350px;;overflow: hidden">
            <template
              v-for="(value, index) in sourceData['机构关键数据折线图']"
            >
              <ZBQHLineChart
                :key="index"
                :chartData="value"
                style="width: 50%;height: 330px;"
              ></ZBQHLineChart>
            </template>
          </div>
        </div>
      </div>
      <div class="content content3">
        <div style="width: 1750px;margin: auto;">
          <header class="header mainTitile">自营关键数据折线图</header>
          <div class="orderArray" style="height: 600;overflow: hident">
            <template
              v-for="(value, index) in sourceData['自营关键数据折线图']"
            >
              <ZBQHLineChart
                :key="index"
                :chartData="value"
                :bgColor="{
                  color: '#F2FF34',
                  color1: 'rgba(242,255,52,0.7)',
                  color2: 'rgba(242,255,52,0.1)'
                }"
                style="width: 50%;height: 300px;"
              ></ZBQHLineChart>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import backgroundImage3 from "@/assets/zaiBeiQieHuan/3-3.jpg";
import ZBQHLineChart from "@/components/ZBQHLineChart";
import websockObject from "@/mixins/websock";
import { right3Data } from "../../../static/zaibeiRightData";
export default {
  name: "ZBQHLeftHeader",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage3,
      sourceData: {},
      interval: null,
      date: "",
      time: ""
    };
  },
  created() {
    this.sourceData = {};
    right3Data.forEach(item => {
      let groupList = {};
      item.data.forEach(elem => {
        groupList[elem] = [];
      });
      this.sourceData[item.group] = groupList;
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.rightWebsockUrl;
    // this.useTheTestDate()
    this.setTime();
  },
  components: {
    ZBQHLineChart
  },
  methods: {
    setTime() {
      this.date = new Date()
        .toLocaleDateString()
        .split("/")
        .join("-");
      this.time = new Date().toTimeString().split(" ")[0];
      this.interval = setInterval(() => {
        this.time = new Date().toTimeString().split(" ")[0];
      }, 1000);
    },
    getWebsocketMsg(sourceData) {
      if (sourceData.length > 0) {
        this.$nextTick(() => {
          this.transformtionData(sourceData);
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      let tillNow = new Date().getTime();
      const sourceData = [
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "量化家-委托笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "量化家-成交笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "自营关键数据折线图",
          indicateName: "委托笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "自营关键数据折线图",
          indicateName: "成交笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "自营关键数据折线图",
          indicateName: "交易指令数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "自营关键数据折线图",
          indicateName: "操作流水数据",
          time: tillNow
        }
      ];
      this.$nextTick(() => {
        this.transformtionData(sourceData);
      });
      let _this = this;
      this.timeOut && clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => {
        _this.useTheTestDate();
      }, 2000);
    },
    transformtionData(data) {
      let groupList = [
        "零售关键数据折线图",
        "机构关键数据折线图",
        "自营关键数据折线图"
      ];
      groupList.forEach(item => {
        const group = data.filter(elem => elem.group === item);
        group.forEach(element => {
          if (
            this.sourceData[item] &&
            this.sourceData[item][element.indicateName]
          ) {
            let dataList = JSON.parse(
              JSON.stringify(this.sourceData[item][element.indicateName])
            );
            if (
              this.sourceData[item][element.indicateName] &&
              this.sourceData[item][element.indicateName].length === 10
            ) {
              dataList.shift();
            }
            dataList.push({
              time: new Date(parseInt(element.time))
                .toTimeString()
                .split(" ")[0]
                .split(":")
                .splice(0, 2)
                .join(":"),
              value: element.value,
              title: element.indicateName
            });
            this.$set(this.sourceData[item], element.indicateName, dataList);
          }
        });
      });
      this.$forceUpdate();
    }
  },
  beforeDestroy() {
    clearInterval(this.interval);
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.content {
  width: 1835px;
  margin-left: 20px;
  overflow: hidden;
}
.weatherTime {
  height: 300px;
}
.header {
  width: 1750px;
  margin: auto;
  text-align: left;
  font-size: 36px;
  height: 100px;
  line-height: 100px;
}
.orderArray {
  position: relative;
  overflow: auto;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
.content1 {
  height: 720px;
  margin-top: 18px;
}
.content2 {
  height: 350px;
}
.content3 {
  height: 720px;
}
</style>



