    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          let fixKey = element.group;
          if (
            self.configObject.groupOrder !== undefined &&
            self.configObject.groupOrder.includes(fixKey)
          ) {
            let typeIndex = self.configObject.groupOrder.indexOf(fixKey);
            self.handerDate(element, typeIndex + 1);
          }
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    handerDate(sour, type) {
      let historyDate = [];
      switch (type) {
        case 1:
          historyDate = this.humidityLeftDate;
          break;
        case 2:
          historyDate = this.electricLeftDate;
          break;
        case 3:
          historyDate = this.usingLeftDate;
          break;
        case 4:
          historyDate = this.doorLeftDate;
          break;

        case 5:
          historyDate = this.humidityRightDate;
          break;
        case 6:
          historyDate = this.electricRightDate;
          break;
        case 7:
          historyDate = this.usingLeftDate;
          break;
        case 8:
          historyDate = this.doorRightate;
          break;
        default:
          console.log("toJiGui");
          break;
      }
      let target = historyDate.findIndex(item => {
        return item.indicateName === sour.indicateName;
      });
      if (target === -1) {
        historyDate.push(sour);
      } else {
        historyDate[target] = sour;
      }
      switch (type) {
        case 1:
          this.humidityLeftDate = historyDate;
          break;
        case 2:
          this.electricLeftDate = historyDate;
          break;
        case 3:
          this.usingLeftDate = historyDate;
          break;
        case 4:
          this.doorLeftDate = historyDate;
          break;

        case 5:
          this.humidityRightDate = historyDate;
