<template>
  <div style="position: relative;overflow: hident;float: right;">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage2 + ')' }"
    ></section>
    <div class="BigContent">
      <div class="content content2">
        <div style="width: 1750px;margin: auto;">
          <header class="header mainTitile">机构关键数据折线图</header>
          <div class="orderArray" style="height: 970px;overflow: hidden;">
            <template
              v-for="(value, index) in sourceData['机构关键数据折线图']"
            >
              <ZBQHLineChart
                :key="index"
                :chartData="value"
                style="width: 50%;height: 330px;"
              ></ZBQHLineChart>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import backgroundImage2 from "@/assets/zaiBeiQieHuan/3-2.jpg";
import { right2Data } from "../../../static/zaibeiRightData";
import ZBQHLineChart from "@/components/ZBQHLineChart";
import websockObject from "@/mixins/websock";
export default {
  name: "ZBQHLeftHeader",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage2,
      sourceData: {},
      interval: null,
      date: "",
      time: ""
    };
  },
  created() {
    this.sourceData = {};
    right2Data.forEach(item => {
      let groupList = {};
      item.data.forEach(elem => {
        groupList[elem] = [];
      });
      this.sourceData[item.group] = groupList;
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.rightWebsockUrl;
    // this.useTheTestDate()
    this.setTime();
  },
  components: {
    ZBQHLineChart
  },
  methods: {
    setTime() {
      this.date = new Date()
        .toLocaleDateString()
        .split("/")
        .join("-");
      this.time = new Date().toTimeString().split(" ")[0];
      this.interval = setInterval(() => {
        this.time = new Date().toTimeString().split(" ")[0];
      }, 1000);
    },
    getWebsocketMsg(sourceData) {
      if (sourceData.length > 0) {
        this.$nextTick(() => {
          this.transformtionData(sourceData);
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      let tillNow = new Date().getTime();
      const sourceData = [
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "恒生pb-委托笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "恒生pb-成交笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "迅投pb-委托笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "迅投pb-成交笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "络町pb-委托笔数数据",
          time: tillNow
        },
        {
          dateTime: tillNow,
          value: parseInt(Math.random() * 100),
          title: "零售关键数据折线图",
          group: "机构关键数据折线图",
          indicateName: "络町pb-成交笔数数据",
          time: tillNow
        }
      ];
      this.$nextTick(() => {
        this.transformtionData(sourceData);
      });
    },
    transformtionData(data) {
      let groupList = right2Data.map(item => item.group);
      groupList.forEach(item => {
        const group = data.filter(elem => elem.group === item);
        group.forEach(element => {
          if (
            this.sourceData[item] &&
            this.sourceData[item][element.indicateName]
          ) {
            let dataList = JSON.parse(
              JSON.stringify(this.sourceData[item][element.indicateName])
            );
            if (
              this.sourceData[item][element.indicateName] &&
              this.sourceData[item][element.indicateName].length === 10
            ) {
              dataList.shift();
            }
            dataList.push({
              time: new Date(parseInt(element.time))
                .toTimeString()
                .split(" ")[0]
                .split(":")
                .splice(0, 2)
                .join(":"),
              value: element.value,
              title: element.indicateName
            });
            this.$set(this.sourceData[item], element.indicateName, dataList);
          }
        });
      });
      this.$forceUpdate();
    }
  },
  beforeDestroy() {
    clearInterval(this.interval);
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.content {
  width: 1835px;
  margin-left: 20px;
  overflow: hidden;
}
.weatherTime {
  height: 300px;
}
.header {
  width: 1750px;
  margin: auto;
  text-align: left;
  font-size: 36px;
  height: 100px;
  line-height: 100px;
}
.orderArray {
  position: relative;
  overflow: auto;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
.content1 {
  height: 720px;
  margin-top: 18px;
}
.content2 {
  height: 1420px;
}
.content3 {
  height: 720px;
}
</style>



