<script>
import cloudBG from "@/assets/DailyOperation/shucai/v3_cloud.png";
import cloudLineChart from "@/components/cloudLineChart";
export default {
  name: "cloudCharts",
  data() {
    return {
      cloudBG
    };
  },
  components: {
    cloudLineChart
  },
  props: {
    sourDate: {
      required: false,
      default: () => ({}),
      type: Object
    }
  },
  computed: {
    chartsDate() {
      let sour = this.sourDate.chartsList || [];
      return sour;
    }
  },
  render(h) {
    return (
      <section class="cloudCharts">
        <section class="headerBox">
          {this.sourDate.normalList &&
            this.sourDate.normalList.map(item => {
              return (
                <section
                  class="headerItem"
                  {...{
                    style: { backgroundImage: " url(" + this.cloudBG + ")" }
                  }}
                >
                  <div class="headerItemInfo name">{item.indicateName}</div>
                  <div class="headerItemInfo value">{item.value}</div>
                </section>
              );
            })}
        </section>
        <section class="charsBox">
          <cloudLineChart sourDate={this.sourDate.chartsList}></cloudLineChart>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.cloudCharts {
  width: auto;
  margin: 0;
  height: 300px;
  padding-right: 15px;
}
.headerBox {
  height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.headerItem {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 5px;
  width: 94px;
  height: 80px;
  display: flex;
  flex-flow: column;
}
.name {
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #a3e0ff;
}
.value {
  font-size: 20px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 1px;
  color: #cee4ff;
}
.headerItemInfo {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.charsBox {
  height: 220px;
}
</style>



