<template>
  <section
    :style="{ height: this.contentHeight + 'px', overflow: this.overflow }"
  >
    <div
      class="tabeleheader itemLi"
      :style="{ backgroundImage: 'url(' + tabelHeader + ')' }"
    >
      <slot name="tabeleheader"></slot>
    </div>
    <vue-seamless-scroll
      :data="listData"
      :class-option="optionHover"
      class="seamless-warp"
      :style="{ height: contentHeight + 'px' }"
    >
      <ul class="item">
        <li
          v-for="(item, index) in listData"
          :key="item.title"
          class="itemLi"
          :style="{
            backgroundImage:
              'url(' + (index % 2 === 0 ? tabelHeader1 : tabelHeader2) + ')'
          }"
        >
          <span class="itemTitle" v-text="item.indicateName"></span>
          <span class="itemDate" v-text="item.value + item.unit"></span>
        </li>
      </ul>
    </vue-seamless-scroll>
  </section>
</template>
<script>
import tabelHeader from "@/assets/DailyOperation/shucai/v1_table1.png";
import tabelHeader1 from "@/assets/DailyOperation/shucai/v1_table2.png";
import tabelHeader2 from "@/assets/DailyOperation/shucai/v1_table3.png";
export default {
  data() {
    return {
      tabelHeader,
      tabelHeader1,
      tabelHeader2
    };
  },
  props: {
    sourDate: {
      required: false,
      default: () => [],
      type: Array
    },
    contentHeight: {
      required: false,
      default: 500,
      type: Number
    },
    overflow: {
      required: false,
      default: "auto",
      type: String
    },
    runnring: {
      required: false,
      default: false,
      type: Boolean
    }
  },
  computed: {
    listData() {
      return this.sourDate.length > 0
        ? this.sourDate[0].value.map(item => {
            return item;
          })
        : [];
    },
    optionHover() {
      return {
        singleHeight: 43,
        waitTime: 2500
      };
    }
  }
};
</script>
<style scoped>
ul {
  margin: 0;
  padding: 0;
}
.seamless-warp {
  width: 100%;
  height: 500px;
  overflow: hidden;
}
.itemLi {
  display: flex;
  height: 43px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
}
.itemLi span {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-left: 1px solid #2153ca;
}
.tabeleheader div {
  width: 50%;
  text-align: center;
}
.itemLi span:first-child {
  border-left: none;
}
</style>



